<template>
    <div class="globlgl  globlgl--custom">
        <div class="btns">
            <div class="search float-r">
             <el-input class="search-input" placeholder="操作类型" :maxlength="128" icon="search" v-model="searchInput" :on-icon-click="handleNotworktimeSearch">
             </el-input>
               
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="notworktimeData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="租户号">
                <template slot-scope="scope">
                    <el-button @click="handlenotworktimeCheck(scope.$index, scope.row)" type="text">{{scope.row.vlanId}}</el-button>
                </template>
            </el-table-column>
            <!-- <el-table-column prop="notworktimeId" label="非工作时间ID"></el-table-column> -->
            <el-table-column prop="dateStamp" label="日期" :formatter="dataFormat"></el-table-column>
            <el-table-column prop="startStamp" label="开始时间" ></el-table-column>
            <el-table-column prop="endStamp" label="结束时间" ></el-table-column>
            <el-table-column prop="operatType" label="操作类型"></el-table-column>
            <el-table-column prop="operatData" label="操作数据"></el-table-column>
        </el-table>
        <!--查看弹窗-->
        <el-dialog title="查看非工作时间" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <!-- <div class="item">非工作时间ID: {{showCheck.notworktimeId}}</div> -->
            <div class="item">租户号: {{showCheck.vlanId}}</div>
            <div class="item">操作数据: {{showCheck.operatData}}</div>
            <div class="item">操作类型: {{showCheck.operatType}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    notworktimeList,
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkDateVule = (rule, value, callback) => { //必须填入日期
            if (value == undefined || "" === value) {
                callback(new Error('请输入日期'));
            }else{
                 callback();
            }
          
        };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        notworktimeData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗

        multipleSelection: [],

        formLabelWidth: '80px',
      }
    },
    created() {
      this.getnotworktimeList(); //加载数据
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      //获取分页 总条数
      async getnotworktimeList() {
        try {
          let query = {
            operatType: this.searchInput
          }
          //分页
          const res = await notworktimeList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.notworktimeData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              const tableItem = {
                dateStamp:item.dateStamp,
                createAt:item.createAt,
                endStamp:item.endStamp,
                notworktimeId:item.notworktimeId,
                operatData:item.operatData,
                operatType:item.operatType,
                startStamp:item.startStamp,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vlanId:item.vlanId,
              }
              this.notworktimeData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },

      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getnotworktimeList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleNotworktimeSearch() {
        this.getnotworktimeList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD");   
         }else{
            return '';
         }
      },
      //查看
      handlenotworktimeCheck(index, row) {
        this.dialogFormCheck = true;
        this.$data.showCheck = row;
      },

    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>
<style lang="less">
    .globlgl.globlgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>



