<template>
    <div class="globlgl  globlgl--custom">
        <div class="btns">
            <el-button class="float-l btn" @click="handleSystemconfigAdd">添加</el-button>
            <el-button class="btn" @click="handleBatchDelete">批量删除</el-button>
            <el-button class="btn" @click="flushPropsl">刷新配置</el-button>
            <div class="search float-r">
             <el-input class="search-input" placeholder="配置项编号" icon="search" v-model="searchInput" :on-icon-click="handleSystemconfigSearch">
             </el-input>
               
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="systemconfigData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="companyid" label="租户ID">
                <template slot-scope="scope">
                    <el-button @click="handlesystemconfigCheck(scope.$index, scope.row)" type="text">{{scope.row.companyid}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="confcode" label="配置项编号"></el-table-column>
            <el-table-column prop="confname" label="配置项名称"></el-table-column>
            <el-table-column prop="conftype" label="配置项种类"></el-table-column>
            <el-table-column prop="confvalue" label="配置项值"></el-table-column>
            <el-table-column prop="status" label="是否启用"></el-table-column>
            <el-table-column prop="introduce" label="简介"></el-table-column>
            <el-table-column label="操作" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleSystemconfigDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button type="text" size="small" @click="handleSystemconfigEdit(scope.$index, scope.row)">编辑
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--添加系统配置信息-->
        <el-dialog title="添加系统配置信息" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="systemconfigForm" :rules="formRules" labelPosition="left" ref="systemconfigForm">
                <el-form-item prop="companyid" label="租户ID" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.companyid" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="confcode" label="配置编码" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.confcode" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="confname" label="配置名称" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.confname" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="conftype" label="配置类型" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.conftype" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="confvalue" label="配置值" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.confvalue" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="introduce" label="简介" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.introduce" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="status" label="是否启用" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.status" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="handleSystemconfigSave('systemconfigForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--编辑系统配置信息-->
        <el-dialog title="编辑系统配置信息" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="systemconfigEditForm" :rules="formRules" labelPosition="left" ref="systemconfigEditForm">
                <el-form-item prop="companyid" label="租户ID" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.companyid" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="confcode" label="配置项编号" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.confcode" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="confname" label="配置项名称" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.confname" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="conftype" label="配置项种类" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.conftype" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="confvalue" label="配置项值" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.confvalue" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="introduce" label="简介" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.introduce" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="status" label="是否启用" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.status" auto-complete="off"></el-input>
                    
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false">取 消</el-button>
            <el-button type="primary" @click="handleSystemconfigSave('systemconfigEditForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--查看弹窗-->
        <el-dialog title="查看系统配置信息" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">租户ID: {{showCheck.companyid}}</div>
            <div class="item">配置编号: {{showCheck.confcode}}</div>
            <div class="item">配置名称: {{showCheck.confname}}</div>
            <div class="item">配置类型: {{showCheck.conftype}}</div>
            <div class="item">配置值: {{showCheck.confvalue}}</div>
            <div class="item">创建时间: {{showCheck.createAt}}</div>
            <div class="item">简介: {{showCheck.introduce}}</div>
            <div class="item">是否启用: {{showCheck.status}}</div>
            <div class="item">更新时间: {{showCheck.updateAt}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    systemconfigList,
    savesystemconfig,
    deletesystemconfigByIds,
    flushProps,
  }from '@/api/getData';
    
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkDateVule = (rule, value, callback) => { //必须填入日期
            if (value == undefined || "" === value) {
                callback(new Error('请输入日期'));
            }else{
                 callback();
            }
          
        };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        systemconfigData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗

        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogFormEdit: false, //编辑弹窗
        systemconfigForm: {
          companyid: '',
          confcode: '',
          confname: '',
          conftype: '',
          confvalue: '',
          introduce: '',
          status: '',
        },
        systemconfigEditForm: {
             companyid: '',
             confcode: '',
             confname: '',
             conftype: '',
             confvalue: '',
             introduce: '',
             status: '',
        },
        formLabelWidth: '80px',
        formRules: {
         
        },
      }
    },
    created() {
      this.getsystemconfigList(); //加载数据
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      //获取分页 总条数
      async getsystemconfigList() {
        try {
          let query = {
            confcode: this.searchInput
          }
          //分页
          const res = await systemconfigList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.systemconfigData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              const tableItem = {
                companyid:item.companyid,
                confcode:item.confcode,
                confname:item.confname,
                conftype:item.conftype,
                confvalue:item.confvalue,
                createAt:item.createAt,
                introduce:item.introduce,
                status:item.status,
                updateAt:item.updateAt,
                uuid:item.uuid,
              }
              this.systemconfigData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      handSaveParam(args){
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getsystemconfigList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleSystemconfigSearch() {
        this.getsystemconfigList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
         }else{
            return '';
         }
      },
      //增加
      handleSystemconfigAdd() {
        this.systemconfigForm = {
          companyid: '',
          confcode: '',
          confname: '',
          conftype: '',
          confvalue: '',
          introduce: '',
          status: '',
        };
        this.dialogFormVisible = true;
      },
      //关闭弹框时重置表单
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deletesystemconfigByIds(data);
              if (result.resultCode == '0') {
                that.getsystemconfigList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleSystemconfigDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deletesystemconfigByIds(data);
            if (result.resultCode == '0') {
              that.getsystemconfigList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //编辑
      handleSystemconfigEdit(index, row) {
        if (typeof row !== 'undefined') {
            this.systemconfigEditForm.companyid = row.companyid;
            this.systemconfigEditForm.confcode = row.confcode;
            this.systemconfigEditForm.confname = row.confname;
            this.systemconfigEditForm.conftype = row.conftype;
            this.systemconfigEditForm.confvalue = row.confvalue;
            this.systemconfigEditForm.introduce = row.introduce;
            this.systemconfigEditForm.status = row.status;
            this.systemconfigEditForm.uuid = row.uuid;
        }
        this.dialogFormEdit = true;
      },
      //查看
      handlesystemconfigCheck(index, row) {
        this.dialogFormCheck = true;
        row.createAt=this.dateFormat(row.createAt);
        row.updateAt=this.dateFormat(row.updateAt);
        this.$data.showCheck = row;
      },
      dateFormat(time) {
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");
         }else{
            return '';
         }
      },
      //添加 编辑的 保存
      handleSystemconfigSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //数据拿出来，如果为空则去掉
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              const result = await savesystemconfig(params);
              if (result.resultCode == '0') {
                  if(formName == 'systemconfigForm'){//添加
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getsystemconfigList();
                  this.$message({
                     type: 'success',
                     message: '系统配置信息保存成功'
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: '保存失败'
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
      async flushPropsl() {
        try {
        //分页
        const res = await flushProps();
        if (res.resultCode == '0') {
            this.$message({
                type: 'info',
                message: '刷新成功'
            });
        } else {
            throw new Error(res.message)
        }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>
<style lang="less">
    .globlgl.globlgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>



