<template>
    <div class="globlgl  globlgl--custom">
        <div class="btns">
            <el-button class="float-l btn" @click="handleUploadfileAdd">添加</el-button>
            <el-button class="btn" @click="handleBatchDelete">批量删除</el-button>
            <div class="search float-r">
             <el-input class="search-input" :maxlength="255" placeholder="原始文件名称" icon="search" v-model="searchInput" :on-icon-click="handleUploadfileSearch">
             </el-input>
               
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="uploadfileData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="租户号"></el-table-column>
            <el-table-column prop="filename" label="原始文件名称">
                <template slot-scope="scope">
                    <el-button @click="handleuploadfileCheck(scope.$index, scope.row)" type="text">{{scope.row.filename}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="filetype" label="文件类型"></el-table-column>
            <el-table-column prop="filepath" label="文件路径"></el-table-column>
            <el-table-column label="操作" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleUploadfileDelete(scope.$index, scope.row)">删除</el-button> 
                    <!--<el-button type="text" size="small" @click="handleUploadfileEdit(scope.$index, scope.row)">编辑-->
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--添加文件上传-->
        <el-dialog title="文件上传" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="uploadfileForm" :rules="formRules" labelPosition="left" ref="uploadfileForm">
                <el-form-item prop="vlanId" label="租户号" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-input v-model.number="uploadfileForm.vlanId" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="filetype" label="文件类型" :label-width="formLabelWidth">
                    <el-select v-model="uploadfileForm.filetype" placeholder="请选择">
                        <el-option  v-for="item in fileType" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
            </el-form>
            <el-upload class="upload-demo"
                :action="importFile" with-credentials :before-upload="beforeUpload"
                :data="uploaddata"  :on-remove="handleRemove" :on-success="onsucess">
                <div class="el-upload__text" style="color:red;margin-bottom:10px;">点我上传</div>
            </el-upload>

            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="handleUploadfileSave('uploadfileForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--编辑文件上传-->
        <el-dialog title="编辑文件上传" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="uploadfileEditForm" :rules="formRules" labelPosition="left" ref="uploadfileEditForm">
                <el-form-item prop="vlanId" label="租户号" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-input v-model.number="uploadfileEditForm.vlanId" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="filename" label="原始文件名称" :label-width="formLabelWidth">
                    <el-input v-model="uploadfileEditForm.filename" :maxlength="255" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="filetype" label="文件类型" :label-width="formLabelWidth">
                    <el-input v-model="uploadfileEditForm.filetype" :maxlength="255" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="filepath" label="文件路径" :label-width="formLabelWidth">
                    <el-input v-model="uploadfileEditForm.filepath" :maxlength="255" auto-complete="off"></el-input>
                    
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false">取 消</el-button>
            <el-button type="primary" @click="handleUploadfileSave('uploadfileEditForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--查看弹窗-->
        <el-dialog title="查看文件上传" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">租户ID : {{showCheck.vlanId}}</div>
            <div class="item">原始文件名称: {{showCheck.filename}}</div>
            <div class="item">文件类型: {{showCheck.filetype}}</div>
            <div class="item">上传者ID: {{showCheck.uploadUser}}</div>
            <div class="item">更新时间: {{showCheck.updateAt}}</div>
            <div class="item">创建时间: {{showCheck.createAt}}</div>
            <div class="item">文件路径: {{showCheck.filepath}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    uploadfileList,
    saveuploadfile,
    deleteuploadfileByIds,
    importFile,
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkDateVule = (rule, value, callback) => { //必须填入日期
            if (value == undefined || "" === value) {
                callback(new Error('请输入日期'));
            }else{
                 callback();
            }
          
        };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        uploadfileData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗

        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogFormEdit: false, //编辑弹窗
        uploadfileForm: {
          filename: '',
          filetype: '',
          filepath: '',
          vlanId:'',
        },
        uploadfileEditForm: {
             filename: '',
             filetype: '',
             filepath: '',
             vlanId:'',
        },
        formLabelWidth: '80px',
        formRules: {
         vlanId: [{required: true,  validator: checkNumber, message:'请输入租户号(数字)', trigger: 'blur'}],
        },
        importFile: importFile,
        fileType:[{
          value: 'ivr',
          label: 'IVR'
          },{
          value: 'meeting',
          label: '会议'
          },{
          value: 'ext',
          label: '分机'
          },{
          value: 'voicemessage',
          label: '语音留言'
          },{
          value: 'other',
          label: '其他'
          }],
          uploaddata:{type:'other'},
          filenumber: 0,
          uploadRes: '',
      }
    },
    created() {
      this.getuploadfileList(); //加载数据
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      //获取分页 总条数
      async getuploadfileList() {
        try {
          let query = {
            filename: this.searchInput
          }
          //分页
          const res = await uploadfileList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.uploadfileData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              const tableItem = {
                createAt:item.createAt,
                filename:item.filename,
                filepath:item.filepath,
                filetype:item.filetype,
                updateAt:item.updateAt,
                uploadUser:item.uploadUser,
                uuid:item.uuid,
                vlanId:item.vlanId,
              }
              this.uploadfileData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      handSaveParam(args){
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getuploadfileList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleUploadfileSearch() {
        this.getuploadfileList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
         }else{
            return '';
         }
      },
      //增加
      handleUploadfileAdd() {
        this.uploadfileForm = {
          vlanId: 0,
          filename: '',
          filetype: 'ivr',
          filepath: '',
        };
        this.$data.filenumber = 0;
        this.dialogFormVisible = true;
      },
      //关闭弹框时重置表单
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              let paths = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
                paths.push(s.filepath)
              })
              let deletedIdsString = deletedIdArr.join(',');
               let pathsString = paths.join(',');
              let data = {
                uuid: deletedIdsString,
                path: pathsString,
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deleteuploadfileByIds(data);
              if (result.resultCode == '0') {
                that.getuploadfileList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleUploadfileDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let path = row.filepath;
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString,
              path: path
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deleteuploadfileByIds(data);
            if (result.resultCode == '0') {
              that.getuploadfileList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //编辑
      handleUploadfileEdit(index, row) {
        if (typeof row !== 'undefined') {
            this.uploadfileEditForm.vlanId = row.vlanId;
            this.uploadfileEditForm.filename = row.filename;
            this.uploadfileEditForm.filetype = row.filetype;
            this.uploadfileEditForm.filepath = row.filepath;
            this.uploadfileEditForm.uuid = row.uuid;
        }
        this.dialogFormEdit = true;
      },
      //查看
      handleuploadfileCheck(index, row) {
        this.dialogFormCheck = true;
        row.createAt=this.dateFormat(row.createAt);
        row.updateAt=this.dateFormat(row.updateAt);
        this.$data.showCheck = row;
      },
      dateFormat(time) {
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");
         }else{
            return '';
         }
      },
      //添加 编辑的 保存
      handleUploadfileSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //数据拿出来，如果为空则去掉
              var args = this.$refs[formName].model;
              if(formName == 'uploadfileForm'){
                   var arr = this.$data.uploadRes.split("=");
                   args.filename = arr[0];
                   args.filepath = arr[1]
              }
              var params = this.handSaveParam(args);
              const result = await saveuploadfile(params);
              if (result.resultCode == '0') {
                  if(formName == 'uploadfileForm'){//添加
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getuploadfileList();
                  this.$message({
                     type: 'success',
                     message: '文件上传保存成功'
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: '保存失败'
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
      beforeUpload(file){
          var type = this.$data.uploadfileForm.filetype;
          if(type == undefined|| type == ''){
                this.$message({
                  type: 'error',
                  message: '文件类型必须选择！'
               });
                return false;
          }
         if(this.$data.filenumber != 0){
             return false;
         }
          this.$data.uploaddata.type = type;
          this.$data.filenumber = 1;
         return true;
      },
       handleRemove(file, fileList) {
           if(file.response != undefined && this.$data.filenumber  == 1){
                this.$data.filenumber = 0;
            }
      },
      onsucess(response,file, fileList){
          this.$data.uploadRes = response.result;
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>
<style lang="less">
    .globlgl.globlgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>



