<template>
    <div class="globlgl  globlgl--custom">
        <div class="btns">
            <el-button class="float-l btn" @click="handleUserAdd">添加</el-button>
            <el-button class="btn" @click="handleBatchDelete">批量删除</el-button>
            <div class="search float-r">
             <el-input class="search-input" placeholder="用户名" :maxlength="32" icon="search" v-model="searchInput" :on-icon-click="handleUserSearch">
             </el-input>
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="userData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="租户号">
                <template slot-scope="scope">
                    <el-button @click="handleuserCheck(scope.$index, scope.row)" type="text">{{scope.row.vlanId}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="username" label="用户名"></el-table-column>
            <el-table-column label="操作" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleUserDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button type="text" size="small" @click="handleUserEdit(scope.$index, scope.row)">编辑
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--添加用户信息-->
        <el-dialog title="添加用户信息" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="userForm" :rules="formRules" labelPosition="left" ref="userForm">
                <el-form-item prop="username" label="用户名" :label-width="formLabelWidth">
                    <el-input v-model="userForm.username" :maxlength="32" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="password" label="密码" :label-width="formLabelWidth">
                    <el-input v-model="userForm.password" :maxlength="32" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="vlanId" label="租户号" :label-width="formLabelWidth">
                    <el-select v-model.number="userForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in companyInfo" :key="item.value" :label="item.value" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="role" label="角色"  :label-width="formLabelWidth">
                    <el-select v-model="userForm.role" placeholder="请选择">
                        <el-option  v-for="item in roleInfo" :key="item.value" :label="item.label" :value="item.label">
                        </el-option>
                    </el-select> 
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="handleUserSave('userForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--编辑用户信息-->
        <el-dialog title="编辑用户信息" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="userEditForm" :rules="formRules" labelPosition="left" ref="userEditForm">
                <el-form-item prop="username" label="用户名" :label-width="formLabelWidth">
                    <el-input v-model="userEditForm.username" :maxlength="32" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="vlanId" label="租户号" :label-width="formLabelWidth">
                    <el-select v-model.number="userEditForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in companyInfo" :key="item.value" :label="item.value" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="role" label="角色" :label-width="formLabelWidth">
                    <el-select v-model="userEditForm.role" placeholder="请选择">
                        <el-option  v-for="item in roleInfo" :key="item.value" :label="item.label" :value="item.label">
                        </el-option>
                    </el-select> 
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false">取 消</el-button>
            <el-button type="primary" @click="handleUserSave('userEditForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--查看弹窗-->
        <el-dialog title="查看用户信息" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">用户名: {{showCheck.username}}</div>
            <div class="item">租户号: {{showCheck.vlanId}}</div>
            <div class="item">创建时间: {{showCheck.createAt}}</div>
            <div class="item">更新时间: {{showCheck.updateAt}}</div>
            <div slot="footer" class="dialog-footer">
              <el-button @click="dialogFormCheck = false">取 消</el-button>
              <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    userList,
    saveuser,
    deleteuserByIds,
    selectAllRole,
    selectAllCompany
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  import moment from 'moment';
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkDateVule = (rule, value, callback) => { //必须填入日期
            if (value == undefined || "" === value) {
                callback(new Error('请输入日期'));
            }else{
                 callback();
            }
          
        };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        userData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗
        roleInfo: [],//角色信息
        companyInfo: [],//租户信息
        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogFormEdit: false, //编辑弹窗
        userForm: {
          vlanId: '',
          username: '',
          role: '',
          password: '',
        },
        userEditForm: {
             vlanId: '',
             username: '',
             role: '',
        },
        formLabelWidth: '80px',
        formRules: {
         
        },
      }
    },
    created() {
      this.getuserList(); //加载数据
      this.selectAllRole();
      this.selectAllCompany()
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      //获取分页 总条数
      async getuserList() {
        try {
          let query = {
            username: this.searchInput
          }
          //分页
          const res = await userList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.userData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              const tableItem = {
                createAt:item.createAt,
                vlanId:item.vlanId,
                updateAt:item.updateAt,
                username:item.username,
                role:item.roles,
                uuid:item.uuid,
              }
              this.userData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      async selectAllRole() {
        try {
            const res = await selectAllRole();
            if (res.resultCode == '0') {
                for (var value of res.result){
                   var temp = {};
                   if(value.name === '普通分机用户')continue;
                   if(this.$store.baseStore.getters.getUserInfo.managerLevel!='999' && value.name === '运营管理员') continue;
                   temp.label = value.name;
                   temp.value = value.uuid;
                   this.$data.roleInfo.push(temp);
                }
            }else {
               throw new Error(res.result)
            }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },
      async selectAllCompany() {
        try {
            const res = await selectAllCompany();
            if (res.resultCode == '0') {
                for (var value of res.result){
                   var temp = {};
                   temp.value = value.companyid;
                   this.$data.companyInfo.push(temp);
                }
            }else {
               throw new Error(res.result)
            }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },
      handSaveParam(args){
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getuserList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleUserSearch() {
        this.getuserList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
         }else{
            return '';
         }
      },
      //增加
      handleUserAdd() {
        this.userForm = {
          role: '',
          vlanId: '',
          username: '',
          password: '',
        };
        this.dialogFormVisible = true;
      },
      //关闭弹框时重置表单
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deleteuserByIds(data);
              if (result.resultCode == '0') {
                that.getuserList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleUserDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deleteuserByIds(data);
            if (result.resultCode == '0') {
              that.getuserList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //编辑
      handleUserEdit(index, row) {
        if (typeof row !== 'undefined') {
            this.userEditForm.vlanId = row.vlanId;
            this.userEditForm.username = row.username;
            this.userEditForm.role = row.role[0].name;
            this.userEditForm.uuid = row.uuid;
        }
        this.dialogFormEdit = true;
      },
      //查看
      handleuserCheck(index, row) {
        this.dialogFormCheck = true;
        row.createAt=this.dateFormat(row.createAt);
        row.updateAt=this.dateFormat(row.updateAt);
        this.$data.showCheck = row;
      },
      dateFormat(time) {
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");
         }else{
            return '';
         }
      },
      //添加 编辑的 保存
      handleUserSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //数据拿出来，如果为空则去掉
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              const result = await saveuser(params);
              if (result.resultCode == '0') {
                  if(formName == 'userForm'){//添加
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getuserList();
                  this.$message({
                     type: 'success',
                     message: '用户信息保存成功'
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: '保存失败'
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: ' info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>
<style lang="less">
    .globlgl.globlgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>



