<template>
  <div>
  	<el-form v-show="errorNumber === 0">
      <el-form-item style="marginBottom: 0;">
      	<el-button v-show="!actionImportLoading" class="show-upload-file">
          <template>
            <span :class="['file-name-text-show', {'file-selected': importUploadFile && importUploadFile.name}]" :title="importUploadFile && importUploadFile.name">
              {{(importUploadFile && importUploadFile.name) || '请选择要上传的文件'}}
            </span>
          </template>  
        </el-button>
      	<div v-show="actionImportLoading" class="schedule-show">
      		<span>导入中<span class="dot"> ...</span></span>
      		<div :style="{width: `${schedule}%`}"></div>
      	</div>
        <el-button class="file-upload-btn" type="button" :loading="actionImportLoading" @click="clickImportBtn">
          选取文件
          <input ref="importUploadFile" class="file-input" type="file" @change="changeImportFile" :accept="accept || ''" />
        </el-button>
      	<p class="msg--import-file">小提示：单次导入数据量最多1000条</p>
      </el-form-item>
      <div class="footer">
      	<a v-if="templateAPI" class="btn-download-import" :href="templateAPI" target="_blank">下载导入模板</a>
      	<div class="action--import-file">
          <el-button @click="cancelImportFunc">取消</el-button>
  	      <el-button type="primary" @click="submitFunc" :loading="actionImportLoading">提交</el-button>
      	</div>
      </div>
    </el-form>
    <div class="fail-msg" v-show="errorNumber > 0">
      <p>共{{totalNumber}}条，成功{{successNumber}}条，失败{{errorNumber}}条</p>
      <i class="el-icon-warning"></i>
      <a :href="failDetailAPI" target="_blank">下载失败详情文件</a>
    </div>
  </div>
</template>

<script>
	import fetchUploadFile from '@/config/fetchUploadFile.js';

	export default {
		data () {
			return {
				actionImportLoading: false,
				templateAPI: this.downloadTemplateAPI,
				importUploadFile: null,
				schedule: 0,
				scheduleInterval: null,
                successNumber: 0,
                errorNumber: 0,
                totalNumber: 0,
			};
		},
		props: {
			importAPI: {
				required: true,
			},
			downloadTemplateAPI: {
				default: '',
				required: true,
			},
            failDetailAPI: {
                default: '',
            },
            setUploadParams: {
                type: Function,
                default: () => {
                    return {};
                },
            },
            dialogParent: { // 父组件窗口显示绑定的变量
                required: true,
            },
            accept: {
                type: String
            }
		},
		watch: {
			dialogParent (newValue) {
				if (!newValue) {
					this.reset();
					this.importUploadFile = null;
                    this.errorNumber = 0;
                    this.$refs.importUploadFile.value = '';
                }
			}
		},
		beforeDestroy () {
			clearInterval(this.scheduleInterval);
		},
		methods: {
			changeImportFile (e) {
	             this.importUploadFile = e.currentTarget.files[0];
	        },
            clickImportBtn () {
                this.$refs.importUploadFile.click();
            },
            reset() {
                this.schedule = 0;
                clearInterval(this.scheduleInterval);
                this.actionImportLoading = false;
            },
            submitFunc () {
                const importUploadFile = this.importUploadFile;
                if (!importUploadFile) {
                    this.$message({
                        type: 'info',
                        message: '请选择要提交的文件!'
                    });
                    return;
                }
                if (importUploadFile.name.indexOf("xlsx") == -1) {
                    this.$message({
                        type: 'info',
                        message: '必须选择xlsx格式的文件'
                    });
                    this.errorNumber = 0;
                    this.importUploadFile = null;
                    return;
                }
                this.actionImportLoading = true;
                // 模拟上传中
                this.scheduleInterval = setInterval(() => {
                    if (this.actionImportLoading) {
                        if (this.schedule < 99) {
                            this.schedule += 1;
                        }
                    } else {
                        this.schedule = 100;
                    }
                }, 100);
                const params = this.setUploadParams(importUploadFile); // 设置请求参数

                fetchUploadFile(importUploadFile, this.importAPI, params)
                    .then(res => {
                        if (res.resultCode == '0') {
                            const result = res.result;
                            this.schedule = 100;
                            this.successNumber = result.successNumber || 0;
                            this.errorNumber = result.errorNumber || 0;
                            this.totalNumber = result.total || 0;
                            if (this.errorNumber === 0) {
                                this.importUploadFile = null;
                            }
                            this.$emit('successCallback',res);//调用父组件的回调方法
                        } else {
                            this.$message({
                                type: 'error',
                                message: '上传失败!'+res.result,
                            });
                           
                        }
                        this.reset();
                    })
                    .catch(err => {
                        this.reset();
                        console.log('获取数据失败', err);
                    });
			    },
                cancelImportFunc () {
                    this.reset();
                    this.importUploadFile = null;
                    this.$emit('cancel');
                }
		},
	}
</script>

<style lang="less" scoped>
	.file-upload-btn {
		margin-left: 5px;
    width: 90px;
    position: relative;
    color: #fff;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    background: #00a8d7;
    border: none;
    border-radius: 3px;

    .file-input {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      ellipsis: hidden;
      display: none;
      cursor: pointer;
    }
  }
  .btn-download-import {
    margin: 28px 0 0 20px;
    float: left;
    color: #40BDDF;
  }
  .footer {
    padding-right: 16px;
    margin: 0 -20px -14px;
    border-top: 1px solid #e6e6e6;
    overflow: hidden;
  }
  .msg--import-file {
    font-size: 12px;
    color: #aaa;
  }
  .action--import-file {
  	float: right;
  }
  .show-upload-file {
  	width: 360px;
    text-align: left;
    color: #999;
    border: 1px solid #ddd;
  }
  .schedule-show {
  	width: 360px;
  	height: 34px;
    display: inline-block;
    position: relative;
    vertical-align: middle;

    & > span {
    	position: absolute;
    	top: -14px;

      .dot {
	      width: 3ch;
	    	display: inline-block; 
      	text-indent: -2ch;
	      vertical-align: bottom; 
	      overflow: hidden;
	      animation: dot 3s infinite step-start both;
	      font-family: Consolas, Monaco, monospace;
      }
    }
    & > div {
    	height: 8px;
    	position: absolute;
    	top: 22px;
    	background: #00A8D5; 
    	border-radius: 6px;
    }
  }
  .fail-msg {
    padding: 0 0 20px;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    text-align: center;

    & > * {
      width: 100%;
    }
    .el-icon-warning {
      display: inline-block;
      font-size: 50px;
      color: #aaa;
    }
    & > a {
      color: #40BDDF;
    }
  }
  @keyframes dot {
    33% { text-indent: -1ch; }
    66% { text-indent: 0; }
	}
  .file-name-text-show {
    width: 330px;
    line-height: 12px;
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    &.file-selected {
      color: #3e3e3e;
    }
  }
</style>


