<template>
    <div class="globlgl">
        <div class="btns">
            <el-button class="float-l btn" @click="rebackEvent" v-show="rebackButton">返回</el-button>
            <el-button class="float-l btn" @click="handleCallerBlackListsAdd">添加</el-button>
            <el-button class="btn" @click="handleBatchDelete">批量删除</el-button>
            <el-button class="btn" @click="blackImportDialogVisible = true">导入</el-button>
            <div class="search float-r">
             <el-input class="search-input" placeholder="屏蔽类型" icon="search" v-model="searchInput" :on-icon-click="handleCallerBlackListsSearch">
             </el-input>
               
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="callerBlackListsData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="租户号">
                <template slot-scope="scope">
                    <el-button @click="handlecallerBlackListsCheck(scope.$index, scope.row)" type="text">{{scope.row.vlanId}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="blacklistsId" label="序号"></el-table-column>
            <el-table-column prop="prefix" label="号码前缀"></el-table-column>
            <el-table-column prop="blackType" label="屏蔽类型"></el-table-column>
            <el-table-column prop="describe" label="描述"></el-table-column>
            <el-table-column label="操作" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleCallerBlackListsDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button type="text" size="small" @click="handleCallerBlackListsEdit(scope.$index, scope.row)">编辑
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--添加主叫黑名单-->
        <el-dialog title="添加主叫黑名单" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="callerBlackListsForm" :rules="formRules" labelPosition="left" ref="callerBlackListsForm">
                <el-form-item prop="vlanId" label="租户名称" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="callerBlackListsForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="blackType" label="屏蔽类型" :label-width="formLabelWidth">
                    <el-input v-model.number="callerBlackListsForm.blackType" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="prefix" label="号码前缀" :label-width="formLabelWidth">
                    <el-input v-model="callerBlackListsForm.prefix" :maxlength="64" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="describe" label="描述" :label-width="formLabelWidth">
                    <el-input v-model="callerBlackListsForm.describe" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="handleCallerBlackListsSave('callerBlackListsForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--编辑主叫黑名单-->
        <el-dialog title="编辑主叫黑名单" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="callerBlackListsEditForm" :rules="formRules" labelPosition="left" ref="callerBlackListsEditForm">
                <el-form-item prop="vlanId" label="租户名称" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="callerBlackListsEditForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="blackType" label="屏蔽类型" :label-width="formLabelWidth">
                    <el-input v-model.number="callerBlackListsEditForm.blackType" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="prefix" label="号码前缀" :label-width="formLabelWidth">
                    <el-input v-model="callerBlackListsEditForm.prefix" :maxlength="64" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="describe" label="描述" :label-width="formLabelWidth">
                    <el-input v-model="callerBlackListsEditForm.describe"  :maxlength="255" auto-complete="off"></el-input>
                    
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false">取 消</el-button>
            <el-button type="primary" @click="handleCallerBlackListsSave('callerBlackListsEditForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--查看弹窗-->
        <el-dialog title="查看主叫黑名单" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">租户号: {{showCheck.vlanId}}</div>
            <div class="item">屏蔽类型: {{showCheck.blackType}}</div>
            <div class="item">号码前缀: {{showCheck.prefix}}</div>
            <div class="item">描述: {{showCheck.describe}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="导入" class="w-500 dialog--import-file" :visible.sync="blackImportDialogVisible" :close-on-click-modal="2==1">
            <import-file-form :importAPI="blackImportListFileAPI" :downloadTemplateAPI="blackTemplateDownloadAPI" 
                :setUploadParams="setUploadParams" :failDetailAPI="blackImportFailedFileAPI" :dialogParent="blackImportDialogVisible"
                @cancel="blackImportDialogVisible = false" @successCallback="importSuccessCallback">
             </import-file-form>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    callerBlackListsList,
    savecallerBlackLists,
    deletecallerBlackListsByIds,

    callerBlackListsTemplateDownload,
    callerBlackListsImport,
    blackImportFailedFileAPI,
  }from '@/api/getData';
    
  import importFileForm from '../call/importFileForm.vue'
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
      };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        callerBlackListsData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗

        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogFormEdit: false, //编辑弹窗
        callerBlackListsForm: {
          vlanId: 0,
          blackType: '',
          prefix: '',
          describe: '',
        },
        callerBlackListsEditForm: {
          vlanId: 0,
          blackType: '',
          prefix: '',
          describe: '',
        },
        formLabelWidth: '80px',
        formRules: {
           vlanId: [{required: true,validator:checkNumber, message:'请输入租户号(数字)',trigger: 'blur'}],
           blackType: [{validator: checkNull, trigger: 'blur'}],
         
        },

        blackImportDialogVisible: false,
        blackImportListFileAPI: callerBlackListsImport,
        blackTemplateDownloadAPI: callerBlackListsTemplateDownload,
        blackImportFailedFileAPI: blackImportFailedFileAPI,
        currentUserVlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
      }
    },
    created() {
      this.getcallerBlackListsList(); //加载数据
    },
    mounted(){
         var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
    },
    components: {
      importFileForm: importFileForm,
    },
    props: {
        rebackButton: false,
        companyId: '',
    },
    watch:{
        companyId: function (val, oldVal){
            this.getcallerBlackListsList(); 
        },
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;
        }
    },
    methods: {
      //获取分页 总条数
      async getcallerBlackListsList() {
        try {
          let query = {
            admincompanyId:this.companyId,
            blackType: this.searchInput
          }
          //分页
          const res = await callerBlackListsList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.callerBlackListsData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              const tableItem = {
                blackType:item.blackType,
                blacklistsId:item.blacklistsId,
                createAt:item.createAt,
                describe:item.describe,
                prefix:item.prefix,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vlanId:item.vlanId,
              }
              this.callerBlackListsData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      handSaveParam(args){
          if(args.blackType == undefined || args.blackType === '')delete args.blackType;
         return args;
      },
      handleSizeChange(val) {
      },
      rebackEvent(){
            var args =  new Object();
            this.$emit('rebackEvent',args);
     },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getcallerBlackListsList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleCallerBlackListsSearch() {
        this.getcallerBlackListsList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
         }else{
            return '';
         }
      },
      //增加
      handleCallerBlackListsAdd() {
        var tempVlanId = this.$data.currentUserVlanId;
        if(this.companyId != undefined && this.companyId != ''){
            tempVlanId = parseInt(this.companyId);
        }
        this.callerBlackListsForm = {
          blackType: '',
          prefix: '',
          describe: '',
          vlanId: tempVlanId,
        };
        this.dialogFormVisible = true;
      },
      //关闭弹框时重置表单
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deletecallerBlackListsByIds(data);
              if (result.resultCode == '0') {
                that.getcallerBlackListsList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleCallerBlackListsDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deletecallerBlackListsByIds(data);
            if (result.resultCode == '0') {
              that.getcallerBlackListsList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //编辑
      handleCallerBlackListsEdit(index, row) {
        if (typeof row !== 'undefined') {
            this.callerBlackListsEditForm.blackType = row.blackType;
            this.callerBlackListsEditForm.prefix = row.prefix;
            this.callerBlackListsEditForm.describe = row.describe;
            this.callerBlackListsEditForm.uuid = row.uuid;
            this.callerBlackListsEditForm.vlanId = row.vlanId;
        }
        this.dialogFormEdit = true;
      },
      //查看
      handlecallerBlackListsCheck(index, row) {
        this.dialogFormCheck = true;
        this.$data.showCheck = row;
      },
      //添加 编辑的 保存
      handleCallerBlackListsSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //数据拿出来，如果为空则去掉
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              const result = await savecallerBlackLists(params);
              if (result.resultCode == '0') {
                  if(formName == 'callerBlackListsForm'){//添加
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getcallerBlackListsList();
                  this.$message({
                     type: 'success',
                     message: '主叫黑名单保存成功'
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: '保存失败'
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },

       // 设置导入黑名单参数
      setUploadParams(importUploadFile) {
        return {
          filedata: importUploadFile.name,
        };
      },
      //黑名单导入成功后回调
      importSuccessCallback (res) {
          this.$data.blackImportDialogVisible = false;
          this.getcallerBlackListsList(); 
          this.$message({
                type: 'success',
                message: res.result,
            });
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>


