<template>
    <div class="globlgl globlgl--custom">
        <div class="btns">
            <el-button class="float-l btn" @click="rebackEvent" v-show="rebackButton">返回</el-button>
            <el-button class="float-l btn" @click="handleIvrFlowMenuAdd">添加</el-button>
            <el-button class="btn" @click="handleBatchDelete">批量删除</el-button>
            <div class="search float-r">
                <el-input class="search-input" placeholder="IVR主菜单名"  :maxlength="128" icon="search" v-model="searchInput" :on-icon-click="handleIvrFlowMenuSearch">
                </el-input>
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="ivrFlowMenuData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="租户号">
                <template slot-scope="scope">
                    <el-button @click="handleivrFlowMenuCheck(scope.$index, scope.row)" type="text">{{scope.row.vlanId}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="ivrMenuId" label="IVR主菜单号"></el-table-column>
            <el-table-column prop="ivrMenuName" label="IVR主菜单名"></el-table-column>
            <el-table-column prop="dtmfLen" label="按键收号长度" width="130"></el-table-column>
            <el-table-column prop="dtmfInputTimeout" label="按键超时(毫秒)" width="140"></el-table-column>
            <el-table-column prop="maxFailures" label="失败次数"  width="130"></el-table-column>
            <el-table-column prop="maxTimeouts" label="最大超时次数"  width="130"></el-table-column>
            <el-table-column label="操作" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleIvrFlowMenuDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button type="text" size="small" @click="handleIvrFlowMenuEdit(scope.$index, scope.row)">编辑
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--添加IVR菜单-->
        <el-dialog title="添加IVR菜单" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="ivrFlowMenuForm" :rules="formRules" labelPosition="left" ref="ivrFlowMenuForm">
                <el-form-item prop="vlanId" label="租户名称" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                     <el-select v-model.number="ivrFlowMenuForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="ivrMenuId" label="IVR主菜单号" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrFlowMenuForm.ivrMenuId" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="ivrMenuName" label="IVR主菜单名" :label-width="formLabelWidth">
                    <el-input v-model="ivrFlowMenuForm.ivrMenuName" :maxlength="128" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="firstSound" label="初始播放的音频文件" :label-width="formLabelWidth">
                    <el-input v-model="ivrFlowMenuForm.firstSound" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="invalidSound" label="错误播放文件名" :label-width="formLabelWidth">
                    <el-input v-model="ivrFlowMenuForm.invalidSound" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="shortSound" label="重复播放文件名" :label-width="formLabelWidth">
                    <el-input v-model="ivrFlowMenuForm.shortSound" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="exitSound" label="退出音频" :label-width="formLabelWidth">
                    <el-input v-model="ivrFlowMenuForm.exitSound" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="dtmfLen" label="按键收号长度" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrFlowMenuForm.dtmfLen" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="dtmfInputTimeout" label="按键超时(毫秒)" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrFlowMenuForm.dtmfInputTimeout" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="maxFailures" label="失败次数" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrFlowMenuForm.maxFailures" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="maxTimeouts" label="最大超时次数" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrFlowMenuForm.maxTimeouts" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="timeout" label="超时时长" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrFlowMenuForm.timeout" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="description" label="备注" :label-width="formLabelWidth">
                    <el-input v-model="ivrFlowMenuForm.description" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="handleIvrFlowMenuSave('ivrFlowMenuForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--编辑IVR菜单-->
        <el-dialog title="编辑IVR菜单" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="ivrFlowMenuEditForm" :rules="formRules" labelPosition="left" ref="ivrFlowMenuEditForm">
                <el-form-item prop="vlanId" label="租户名称" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="ivrFlowMenuEditForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="ivrMenuId" label="IVR主菜单号" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrFlowMenuEditForm.ivrMenuId" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="ivrMenuName" label="IVR主菜单名" :label-width="formLabelWidth">
                    <el-input v-model="ivrFlowMenuEditForm.ivrMenuName"  :maxlength="128" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="firstSound" label="初始播放的音频文件" :label-width="formLabelWidth">
                    <el-input v-model="ivrFlowMenuEditForm.firstSound" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="invalidSound" label="错误播放文件名" :label-width="formLabelWidth">
                    <el-input v-model="ivrFlowMenuEditForm.invalidSound" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="shortSound" label="重复播放文件名" :label-width="formLabelWidth">
                    <el-input v-model="ivrFlowMenuEditForm.shortSound" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="exitSound" label="退出音频" :label-width="formLabelWidth">
                    <el-input v-model="ivrFlowMenuEditForm.exitSound" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="dtmfLen" label="按键收号长度" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrFlowMenuEditForm.dtmfLen" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="dtmfInputTimeout" label="按键超时(毫秒)" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrFlowMenuEditForm.dtmfInputTimeout" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="maxFailures" label="失败次数" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrFlowMenuEditForm.maxFailures" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="maxTimeouts" label="最大超时次数" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrFlowMenuEditForm.maxTimeouts" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="timeout" label="超时时长" :label-width="formLabelWidth">
                    <el-input v-model.number="ivrFlowMenuEditForm.timeout" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="description" label="备注" :label-width="formLabelWidth">
                    <el-input v-model="ivrFlowMenuEditForm.description" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false">取 消</el-button>
            <el-button type="primary" @click="handleIvrFlowMenuSave('ivrFlowMenuEditForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--查看弹窗-->
        <el-dialog title="查看IVR菜单" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">租户号: {{showCheck.vlanId}}</div>
            <div class="item">IVR主菜单号: {{showCheck.ivrMenuId}}</div>
            <div class="item">IVR主菜单名: {{showCheck.ivrMenuName}}</div>
            <div class="item">初始播放的音频文件: {{showCheck.firstSound}}</div>
            <div class="item">重复播放文件名: {{showCheck.shortSound}}</div>
            <div class="item">错误播放文件名: {{showCheck.invalidSound}}</div>
            <div class="item">退出音频: {{showCheck.exitSound}}</div>
            <div class="item">按键超时(毫秒): {{showCheck.dtmfInputTimeout}}</div>
            <div class="item">按键收号长度: {{showCheck.dtmfLen}}</div>
            <div class="item">失败次数: {{showCheck.maxFailures}}</div>
            <div class="item">最大超时次数: {{showCheck.maxTimeouts}}</div>
            <div class="item">超时时长: {{showCheck.timeout}}</div>
            <div class="item">备注: {{showCheck.description}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    ivrFlowMenuList,
    saveivrFlowMenu,
    deleteivrFlowMenuByIds
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';

  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
      };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        ivrFlowMenuData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗

        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogFormEdit: false, //编辑弹窗
        ivrFlowMenuForm: {
          vlanId: '',
          ivrMenuId: '',
          ivrMenuName: '',
          ivrEntryId: '',
          ivrEntryName: '',
          firstSound: '',
          invalidSound: '',
          exitSound: '',
          dtmfLen: '',
          dtmfInputTimeout: '',
          shortSound: '',
          maxFailures: '',
          maxTimeouts: '',
          timeout: '',
          description: '',
        },
        ivrFlowMenuEditForm: {
             vlanId: '',
             ivrMenuId: '',
             ivrMenuName: '',
             ivrEntryId: '',
             ivrEntryName: '',
             firstSound: '',
             invalidSound: '',
             exitSound: '',
             dtmfLen: '',
             dtmfInputTimeout: '',
             shortSound: '',
             maxFailures: '',
             maxTimeouts: '',
             timeout: '',
             description: '',
        },
        formLabelWidth: '80px',
        formRules: {
           vlanId: [{required: true,message: '请输入租户号(数字)', validator:checkNumber, trigger: 'blur'}],
           ivrMenuName:[{required: true,message: '请输入菜单名称', trigger: 'blur'}],
           ivrMenuId: [{validator: checkNull, trigger: 'blur'}],
           ivrEntryId: [{validator: checkNull, trigger: 'blur'}],
           dtmfLen: [{required: true,message:"请输入按键收号长度",validator: checkNumber, trigger: 'blur'}],
           dtmfInputTimeout: [{required: true,message:"请输入按键超时(毫秒)",validator: checkNumber, trigger: 'blur'}],
           maxFailures: [{required: true,message:"请输入失败次数",validator: checkNumber, trigger: 'blur'}],
           maxTimeouts: [{required: true,message:"请输入最大超时次数",validator: checkNumber, trigger: 'blur'}],
           timeout: [{required: true,message:"请输入超时时长",validator: checkNumber, trigger: 'blur'}],
           firstSound:[{required: true,message: '请输入初始播放的音频文件', trigger: 'blur'}],
           shortSound:[{required: true,message: '请输入重复播放的音频文件', trigger: 'blur'}],
           invalidSound:[{required: true,message: '请输入错误播放的音频文件', trigger: 'blur'}],
           exitSound:[{required: true,message: '请输入退出音频文件', trigger: 'blur'}],
        },
        currentUserVlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
      }
    },
    created() {
      this.getivrFlowMenuList(); //加载数据
    },
    mounted(){
        var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
    },
    components: {
      ElTableTreeColumn
    },
    props: {
        rebackButton: false,
        companyId: '',
    },
    watch:{
        companyId: function (val, oldVal){
            this.getivrFlowMenuList(); 
        },
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;
        }
    },
    methods: {
      //获取分页 总条数
      async getivrFlowMenuList() {
        try {
          let query = {
            admincompanyId:this.companyId,
            ivrMenuName: this.searchInput
          }
          //分页
          const res = await ivrFlowMenuList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.ivrFlowMenuData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              const tableItem = {
                createAt:item.createAt,
                description:item.description,
                dtmfInputTimeout:item.dtmfInputTimeout,
                dtmfLen:item.dtmfLen,
                exitSound:item.exitSound,
                firstSound:item.firstSound,
                invalidSound:item.invalidSound,
                ivrEntryId:item.ivrEntryId,
                ivrEntryName:item.ivrEntryName,
                ivrMenuId:item.ivrMenuId,
                ivrMenuName:item.ivrMenuName,
                maxFailures:item.maxFailures,
                maxTimeouts:item.maxTimeouts,
                shortSound:item.shortSound,
                timeout:item.timeout,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vlanId:item.vlanId,
              }
              this.ivrFlowMenuData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      handSaveParam(args){
         if(args.ivrMenuId == undefined || args.ivrMenuId === '')delete args.ivrMenuId;
         if(args.ivrEntryId == undefined || args.ivrEntryId === '')delete args.ivrEntryId;
         if(args.dtmfLen == undefined || args.dtmfLen === '')delete args.dtmfLen;
         if(args.dtmfInputTimeout == undefined || args.dtmfInputTimeout === '')delete args.dtmfInputTimeout;
         if(args.maxFailures == undefined || args.maxFailures === '')delete args.maxFailures;
         if(args.maxTimeouts == undefined || args.maxTimeouts === '')delete args.maxTimeouts;
         if(args.timeout == undefined || args.timeout === '')delete args.timeout;
         return args;
      },
      handleSizeChange(val) {
      },
      rebackEvent(){
        var args =  new Object();
        this.$emit('rebackEvent',args);
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getivrFlowMenuList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleIvrFlowMenuSearch() {
        this.getivrFlowMenuList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },

      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return moment(time).format("YYYY-MM-DD HH:mm:ss")  
         }else{
            return '';
         }
         
      },
      //增加
      handleIvrFlowMenuAdd() {
         var tempVlanId = this.$data.currentUserVlanId;
        if(this.companyId != undefined && this.companyId != ''){
            tempVlanId = parseInt(this.companyId);
        }
        this.ivrFlowMenuForm = {
          vlanId: tempVlanId,
          ivrMenuId: '',
          ivrMenuName: '',
          ivrEntryId: '',
          ivrEntryName: '',
          firstSound: '',
          invalidSound: '',
          exitSound: '',
          dtmfLen: 4,
          dtmfInputTimeout: 2000,
          shortSound: '',
          maxFailures: 100,
          maxTimeouts: 3,
          timeout: 30000,
          description: '',
        };
        this.dialogFormVisible = true;
      },
      //关闭弹框时重置表单
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deleteivrFlowMenuByIds(data);
              if (result.resultCode == '0') {
                that.getivrFlowMenuList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleIvrFlowMenuDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deleteivrFlowMenuByIds(data);
            if (result.resultCode == '0') {
              that.getivrFlowMenuList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //编辑
      handleIvrFlowMenuEdit(index, row) {
        if (typeof row !== 'undefined') {
            this.ivrFlowMenuEditForm.vlanId = row.vlanId;
            this.ivrFlowMenuEditForm.ivrMenuId = row.ivrMenuId;
            this.ivrFlowMenuEditForm.ivrMenuName = row.ivrMenuName;
            this.ivrFlowMenuEditForm.ivrEntryId = row.ivrEntryId;
            this.ivrFlowMenuEditForm.ivrEntryName = row.ivrEntryName;
            this.ivrFlowMenuEditForm.firstSound = row.firstSound;
            this.ivrFlowMenuEditForm.invalidSound = row.invalidSound;
            this.ivrFlowMenuEditForm.exitSound = row.exitSound;
            this.ivrFlowMenuEditForm.dtmfLen = row.dtmfLen;
            this.ivrFlowMenuEditForm.dtmfInputTimeout = row.dtmfInputTimeout;
            this.ivrFlowMenuEditForm.shortSound = row.shortSound;
            this.ivrFlowMenuEditForm.maxFailures = row.maxFailures;
            this.ivrFlowMenuEditForm.maxTimeouts = row.maxTimeouts;
            this.ivrFlowMenuEditForm.timeout = row.timeout;
            this.ivrFlowMenuEditForm.description = row.description;
            this.ivrFlowMenuEditForm.uuid = row.uuid;
        }
        this.dialogFormEdit = true;
      },
      //查看
      handleivrFlowMenuCheck(index, row) {
        this.dialogFormCheck = true;
        this.$data.showCheck = row;
      },
      //添加 编辑的 保存
      handleIvrFlowMenuSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //数据拿出来，如果为空则去掉
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              const result = await saveivrFlowMenu(params);
              if (result.resultCode == '0') {
                  if(formName == 'ivrFlowMenuForm'){//添加
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getivrFlowMenuList();
                  this.$message({
                     type: 'success',
                     message: 'IVR菜单保存成功'
                  });
              }else if(result.resultCode == '00009'){
                  this.$message({
                    type: 'error',
                    message: result.result,
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: '保存失败'
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
    }
  }

</script>
