export default (file, uri, args = {}, inputName = 'Filedata') => {
	let data = new FormData();

  data.append(inputName, file);
  
  const option ={
    method: 'post',
    credentials: 'include',
    mode: 'cors',
    cache: 'force-cache',                                                   
    body: data,
  };

  let params = '?' ;

  Object.keys(args).forEach(item => {
  	params += `${item}=${args[item]}&`;
  });

  return fetch(`${uri}${params.slice(0, -1)}`, option)
  .then(res => res.json());
};


