<template>
    <div class="globlgl  globlgl--custom">
        <div class="btns">
            <el-button class="float-l btn" @click="handleRoleAdd">添加</el-button>
            <el-button class="btn" @click="handleBatchDelete">批量删除</el-button>
            <div class="search float-r">
                <el-input class="search-input" placeholder="角色名" :maxlength="50" icon="search" v-model="searchInput" :on-icon-click="handleRoleSearch">
                </el-input>
            </div>
        </div>
        <div class="tables">
            <el-table class="table" ref="multipleTable" :data="roleData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="name" label="角色名">
                    <template slot-scope="scope">
                        <el-button @click="handleroleCheck(scope.$index, scope.row)" type="text">{{scope.row.name}}</el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="description" label="描述"></el-table-column>
                <el-table-column label="操作" width="180">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" v-show="scope.row.uuid != '1'" @click="handleRoleDelete(scope.$index, scope.row)">删除</el-button>
                        <el-button type="text" size="small" @click="handleRoleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button type="text" size="small" @click="handleRoleMenu(scope.$index, scope.row)">配置菜单</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--添加角色信息-->
            <el-dialog title="添加角色信息" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
                <el-form :model="roleForm" :rules="formRules" labelPosition="left" ref="roleForm">
                    <el-form-item prop="name" label="角色名" :label-width="formLabelWidth">
                        <el-input v-model="roleForm.name" :maxlength="50" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item prop="description" label="描述" :label-width="formLabelWidth">
                        <el-input v-model="roleForm.description" :maxlength="255" auto-complete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="handleRoleSave('roleForm')">确 定</el-button>
                </div>
            </el-dialog>
            <!--编辑角色信息-->
            <el-dialog title="编辑角色信息" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
                <el-form :model="roleEditForm" :rules="formRules" labelPosition="left" ref="roleEditForm">
                    <el-form-item prop="name" label="角色名" :label-width="formLabelWidth">
                        <el-input v-model="roleEditForm.name" :maxlength="50" auto-complete="off"></el-input>
                        
                    </el-form-item>
                    <el-form-item prop="description" label="描述" :label-width="formLabelWidth">
                        <el-input v-model="roleEditForm.description" :maxlength="255" auto-complete="off"></el-input>
                        
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormEdit = false">取 消</el-button>
                    <el-button type="primary" @click="handleRoleSave('roleEditForm')">确 定</el-button>
                </div>
            </el-dialog>
            <!--查看弹窗-->
            <el-dialog title="查看角色信息" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
                <div class="item">角色名: {{showCheck.name}}</div>
                <div class="item">描述: {{showCheck.description}}</div>
                <div class="item">创建时间: {{showCheck.createAt}}</div>
                <div class="item">更新时间: {{showCheck.updateAt}}</div>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormCheck = false">取 消</el-button>
                    <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
                </div>
            </el-dialog>
            <!--编辑菜单弹窗-->
            <el-dialog title="设置角色菜单" class="w-600" :visible.sync="dialogmenu" :close-on-click-modal="2==1">
                <div>
                    <el-button class="btn" @click="saveMenu">保存菜单</el-button>
                    <el-tree
                        :data="menu"
                        :props="defaultProps"
                        show-checkbox
                        node-key="id"
                        ref="tree"
                        :default-expanded-keys="expandArray"
                        :expand-on-click-node="false"
                        >
                    </el-tree>
                </div>
            </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
            </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    roleList,
    saverole,
    deleteroleByIds,
    getRoleMenu,
    saveRoleMenu,
    selectAllMenu,
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  import moment from 'moment';
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkDateVule = (rule, value, callback) => { //必须填入日期
            if (value == undefined || "" === value) {
                callback(new Error('请输入日期'));
            }else{
                 callback();
            }
          
        };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        roleData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗

        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogFormEdit: false, //编辑弹窗
        roleForm: {
          name: '',
          description: '',
        },
        roleEditForm: {
             name: '',
             description: '',
        },
        formLabelWidth: '80px',
        formRules: {},
        //菜单部分
        dialogmenu:false,
        menu: [],
        menuId:"",
        rolemenuId:"",
        roleId:"",
        expandArray:[1],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
      }
    },
    created() {
      this.getroleList(); //加载数据
      this.getAllMenu();
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
        //获取分页 总条数
        async getroleList() {
            try {
                let query = {
                    name: this.searchInput
                }
                //分页
                const res = await roleList({
                    pageindex:this.currentPage,
                    pagesize:this.limit,
                    query:JSON.stringify(query)
                });
                if (res.resultCode == '0') {
                    this.roleData = [];
                    //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
                    this.showCheck = res.result.data;
                    this.count=res.result.total;
                    res.result.data.forEach(item => {
                    const tableItem = {
                        createAt:item.createAt,
                        description:item.description,
                        name:item.name,
                        updateAt:item.updateAt,
                        uuid:item.uuid,
                        menuId:item.menuId,
                    }
                    this.roleData.push(tableItem)
                    })
                } else {
                    throw new Error(res.message)
                }
            } catch (err) {
            console.log('获取数据失败', err);
            }
        },
        async getAllMenu() {
            try {
                const res = await selectAllMenu();
                if (res.resultCode == '0') {
                    this.menuId = res.result.uuid;
                    this.menu.push(res.result.menu);
                } else {
                    throw new Error(res.message)
                }
            } catch (err) {
                console.log('获取数据失败', err);
            }
        },
        async saveMenu(){
            try {
                var args = new Object();
                args['keys'] = this.$refs.tree.getCheckedKeys().join(",");
                args['roleId'] = this.$data.roleId;
                args['rolemenuId'] = this.$data.rolemenuId;
                const result = await saveRoleMenu(args);
                if (result.resultCode == '0') {
                    this.dialogmenu = false;
                    this.$message({
                        type: 'success',
                        message: '新菜单保存成功'
                    });
                }else{
                    this.$message({
                        type: 'error',
                        message: '保存失败'
                    });
                }
            } catch (err) {
                this.$message({
                    type: 'error',
                    message: '保存失败'
                });
            }
        },
        //编辑
        async handleRoleMenu(index, row) {
            this.dialogmenu = true;
            try {
                if(this.$data.menu.length == 0){
                    this.getAllMenu();
                }
                this.$data.rolemenuId = "";
                this.$data.roleId = row.uuid;
                var rolemenuId =  row.menuId;
                if(rolemenuId != undefined && rolemenuId != ""){ //等于空表示此角色没有配置权限菜单
                    var args = new Object();
                    args['menuId'] = row.menuId;
                    const res = await getRoleMenu(args);
                    if (res.resultCode == '0') {
                         this.$data.rolemenuId = rolemenuId;
                         //this.$refs.tree.setCheckedKeys(res.result.menutext.split(","));
                         let arr = res.result.menutext.split(",");
                         let array = new Array();
                         for(var i=0;i<arr.length;i++){
                            if(arr[i].length == 7){
                                array.push( Number.parseInt(arr[i]));
                            }
                         }
                         this.$refs.tree.setCheckedKeys(array);
                    } else {
                        throw new Error(res.message)
                    }
                }else{
                    this.$refs.tree.setCheckedKeys([]);
                }
            } catch (err) {
                console.log('获取数据失败', err);
            }
        },
        handSaveParam(args){
            return args;
        },
        handleSizeChange(val) {},
      
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getroleList();
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        handleRoleSearch() {
            this.getroleList();
        },
        formatter(row, column) {
            return ' -- ' + row.name;
        },
        dataFormat(row, column) {
            var time =  row[column.property];
            if(time != undefined && time != ''){
                return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
            }else{
                return '';
            }
        },
        //增加
        handleRoleAdd() {
            this.roleForm = {
            name: '',
            description: '',
            };
            this.dialogFormVisible = true;
        },
        //关闭弹框时重置表单
        handleDialogWithFormClose() {
            for (var prop in this.$refs) {
            if (this.$refs.hasOwnProperty(prop)) {
                if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
                this.$refs[prop].resetFields();
                }
            }
            }
        },
        //批量删除
        handleBatchDelete() {
            let that = this;
            if (this.multipleSelection.length === 0) {
            this.$alert('请至少选择一条记录', '提示', {
                confirmButtonText: '确定',
                callback: () => {
                return false;
                }
            });
            } else {
            this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(async() => {
                try {
                let deletedIdArr = [];
                that.multipleSelection.forEach(s => {
                    deletedIdArr.push(s.uuid)
                })
                let deletedIdsString = deletedIdArr.join(',');
                let data = {
                    uuid: deletedIdsString
                }
                //此方法可以和后台协商一致，单条与批量删除使用同一个函数
                const result = await deleteroleByIds(data);
                if (result.resultCode == '0') {
                    that.getroleList();
                    this.$message({
                    type: 'success',
                    message: '删除成功!'
                    });
                }
                } catch (err) {
                console.log(err)
                }
            }).catch(() => {
                this.$message({
                type: 'info',
                message: '已取消删除'
                });
            });
            }
        },
        //删除当前记录
        handleRoleDelete(index, row) {
            let that = this;
            this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(async() => {
            try {
                let deletedIdsString = [row.uuid].join(',');
                let data = {
                uuid: deletedIdsString
                }
                //此方法可以和后台协商一致，单条与批量删除使用同一个函数
                const result = await deleteroleByIds(data);
                if (result.resultCode == '0') {
                that.getroleList();
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
                }
            } catch (err) {
                console.log(err)
            }
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });
            });
        },
        //编辑
        handleRoleEdit(index, row) {
            if (typeof row !== 'undefined') {
                this.roleEditForm.name = row.name;
                this.roleEditForm.description = row.description;
                this.roleEditForm.uuid = row.uuid;
            }
            this.dialogFormEdit = true;
        },
        
        //查看
        handleroleCheck(index, row) {
            this.dialogFormCheck = true;
            row.createAt=this.dateFormat(row.createAt);
            row.updateAt=this.dateFormat(row.updateAt);
            this.$data.showCheck = row;
        },
        dateFormat(time) {
            if(time != undefined && time != ''){
                return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");
            }else{
                return '';
            }
        },
        //添加 编辑的 保存
        handleRoleSave(formName) {
            let that = this;
            this.$refs[formName].validate(async(valid) => {
                if (valid) {
                    try {
                    //数据拿出来，如果为空则去掉
                    var args = this.$refs[formName].model;
                    var params = this.handSaveParam(args);
                    const result = await saverole(params);
                    if (result.resultCode == '0') {
                        if(formName == 'roleForm'){//添加
                        that.dialogFormVisible = false;
                        }else{
                        this.dialogFormEdit = false;
                        }
                        that.getroleList();
                        this.$message({
                        type: 'success',
                        message: '角色信息保存成功'
                        });
                    }else if(result.resultCode == '91006'){
                        this.$message({
                        type: 'error',
                        message: result.result
                        });
                    }else{
                        this.$message({
                        type: 'error',
                        message: '保存失败'
                        });
                    }
                    } catch (err) {
                    this.$message({
                        type: 'error',
                        message: '保存失败'
                    });
                    }
                } else {
                    this.$message({
                        type: 'info',
                        message: '请检查输入是否不合法'
                    });
                    return false;
                }
            });
        },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-600 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>
<style lang="less">
    .globlgl.globlgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>



