<template>
    <div class="globlgl  globlgl--custom">
        <div class="btns">
            <el-button class="float-l btn" @click="handleHolidayAdd">添加</el-button>
            <el-button class="btn" @click="handleBatchDelete">批量删除</el-button>
            <div class="search float-r">
             <el-input class="search-input" placeholder="节假日名称" :maxlength="10" icon="search" v-model="searchInput" :on-icon-click="handleHolidaySearch">
             </el-input>
               
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="holidayData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="year" label="年份">
                <template slot-scope="scope">
                    <el-button @click="handleholidayCheck(scope.$index, scope.row)" type="text">{{scope.row.year}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="name" label="节假日名称"></el-table-column>
            <el-table-column prop="date" label="日期"></el-table-column>
            <el-table-column label="操作" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleHolidayDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button type="text" size="small" @click="handleHolidayEdit(scope.$index, scope.row)">编辑
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--添加节假日-->
        <el-dialog title="添加节假日" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="holidayForm" :rules="formRules" labelPosition="left" ref="holidayForm">
                <el-form-item prop="year" label="年份" :label-width="formLabelWidth">
                    <el-select v-model="holidayForm.year" placeholder="请选择">
                        <el-option  v-for="item in yearArr" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="name" label="节假日名称" :label-width="formLabelWidth">
                    <el-select v-model="holidayForm.name" placeholder="请选择">
                        <el-option  v-for="item in holidayday" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="date" label="日期" :label-width="formLabelWidth">
                    <el-date-picker
                    v-model="dayArray"
                    :editable="editable"
                    format="yyyy 年 MM 月 dd 日"
                    value-format="yyyy-MM-dd"
                    type="daterange"
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期">
                    </el-date-picker>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="handleHolidaySave('holidayForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--编辑节假日-->
        <el-dialog title="编辑节假日" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="holidayEditForm" :rules="formRules" labelPosition="left" ref="holidayEditForm">
                <el-form-item prop="year" label="年份" :label-width="formLabelWidth">
                    <el-select v-model="holidayEditForm.year" placeholder="请选择">
                        <el-option  v-for="item in yearArr" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="name" label="节假日名称" :label-width="formLabelWidth">
                    <el-select v-model="holidayEditForm.name" placeholder="请选择">
                        <el-option  v-for="item in holidayday" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="date" label="日期" :label-width="formLabelWidth">
                    <el-date-picker
                    v-model="dayArray"
                    type="daterange"
                    :editable="editable"
                    value-format="yyyy-MM-dd"
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期">
                    </el-date-picker>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false">取 消</el-button>
            <el-button type="primary" @click="handleHolidaySave('holidayEditForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--查看弹窗-->
        <el-dialog title="查看节假日" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">插入时间: {{showCheck.createAt}}</div>
            <div class="item">日期: {{showCheck.date}}</div>
            <div class="item">节假日名称: {{showCheck.name}}</div>
            <div class="item">更新时间: {{showCheck.updateAt}}</div>
            <div class="item">年份: {{showCheck.year}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    holidayList,
    saveholiday,
    deleteholidayByIds
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkDateVule = (rule, value, callback) => { //必须填入日期
            if (value == undefined || "" === value) {
                callback(new Error('请输入日期'));
            }else{
                 callback();
            }
          
        };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        holidayData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗

        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogFormEdit: false, //编辑弹窗
        holidayForm: {
          year: '',
          name: '',
          date: '',
        },
        holidayEditForm: {
             year: '',
             name: '',
             date: '',
        },
        formLabelWidth: '80px',
        formRules: {},
        yearArr: [],
        toyear:'',
        holidayday: [{
          value: '元旦',
          label: '元旦'
        },{
          value: '春节',
          label: '春节'
        },{
          value: '清明',
          label: '清明'
        },{
          value: '劳动节',
          label: '劳动节'
        },{
          value: '端午',
          label: '端午'
        },{
          value: '中秋',
          label: '中秋'
        },{
          value: '国庆',
          label: '国庆'
        },{
          value: '补班',
          label: '补班'
        }],
       
        editable: false,
        dayArray: [],
      }
    },
    created() {
        this.getholidayList(); //加载数据
        this.initYear();
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      //获取分页 总条数
      async getholidayList() {
        try {
          let query = {
            name: this.searchInput
          }
          //分页
          const res = await holidayList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.holidayData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              const tableItem = {
                createAt:item.createAt,
                date:item.date,
                name:item.name,
                updateAt:item.updateAt,
                uuid:item.uuid,
                year:item.year,
              }
              this.holidayData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      handSaveParam(args){
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getholidayList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleHolidaySearch() {
        this.getholidayList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
         }else{
            return '';
         }
      },
      //增加
      handleHolidayAdd() {
        this.holidayForm = {
          year: this.$data.toyear,
          name: '元旦',
          date: '',
        };
        this.$data.dayArray = [];
        this.$data.dayArray.push(this.$moment(new Date()).format('YYYY-MM-DD'));
        this.$data.dayArray.push(this.$moment(new Date()).format('YYYY-MM-DD'));
        this.dialogFormVisible = true;
      },
      //关闭弹框时重置表单
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deleteholidayByIds(data);
              if (result.resultCode == '0') {
                that.getholidayList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleHolidayDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deleteholidayByIds(data);
            if (result.resultCode == '0') {
              that.getholidayList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //编辑
      handleHolidayEdit(index, row) {
        if (typeof row !== 'undefined') {
            this.holidayEditForm.year = row.year;
            this.holidayEditForm.name = row.name;
            this.holidayEditForm.date = row.date;
            this.holidayEditForm.uuid = row.uuid;
            var dates = row.date;
            this.$data.dayArray = dates.split("至");
        }
        this.dialogFormEdit = true;
      },
      //查看
      handleholidayCheck(index, row) {
        this.dialogFormCheck = true;
        this.$data.showCheck = row;
      },
      //添加 编辑的 保存
      handleHolidaySave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //数据拿出来，如果为空则去掉
              var args = this.$refs[formName].model;
              
              args['date'] =this.$moment(this.$data.dayArray[0]).format('YYYY-MM-DD')+"至"+this.$moment(this.$data.dayArray[1]).format('YYYY-MM-DD');
              var params = this.handSaveParam(args);
              const result = await saveholiday(params);
              if (result.resultCode == '0') {
                  if(formName == 'holidayForm'){//添加
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getholidayList();
                  this.$message({
                     type: 'success',
                     message: '节假日保存成功'
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: '保存失败'
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
      initYear(){
        var today = new Date();
        var year = today.getFullYear();
        var nextYear = year + 1;
        var toyear = {};
        toyear['label'] = year;
        toyear['value'] = year; 
        var next = {};
        next['label'] = nextYear;
        next['value'] = nextYear; 
        this.$data.yearArr.push(toyear);
        this.$data.yearArr.push(next);
        this.$data.toyear = year;
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>
<style lang="less">
    .globlgl.globlgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>



