<script>

  import {
    scheduleList,
    saveschedule,
    deletescheduleByIds,
    getNotworktime,
    getWholeHoliday,
  }from '@/api/getData';
    
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  import moment from 'moment';
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkDateVule = (rule, value, callback) => { //必须填入日期
            if (value == undefined || "" === value) {
                callback(new Error('请输入日期'));
            }else{
                 callback();
            }
          
        };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        scheduleData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗
        freshkey: '1',
        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogFormEdit: false, //编辑弹窗

        //月份
        monthInfo:[{value:'1'},{value:'2'},{value:'3'},{value:'4'},{value:'5'},{value:'6'},{value:'7'},
        {value:'8'},{value:'9'},{value:'10'},{value:'11'},{value:'12'}],
        scheduleForm: {
          vlanId: '',
          scheduleName: '',
          timeMessage:'',
          scheduleStatus: '0',
          description: '',
          startTime:'',
          endTime:'',
          month:'',
          days:[],
          operatType:'留言',
          // operatData:''
        },
        scheduleEditForm: {
          vlanId: '',
          scheduleName: '',
          scheduleStatus: '0',
          timeMessage:'',
          description: '',
          startTime:'',
          endTime:'',
          month:'',
          days:[],
          operatType:'留言',
          // operatData:''
        },
        formLabelWidth: '80px',
        formRules: {
          vlanId: [{required: true,validator:checkNumber, message:'请输入租户号(数字)',trigger: 'blur'}],
          // scheduleName:{ required: true, message: '请输入节假日名称', trigger: 'blur' },
          //scheduleStatus:{required: true, message: '请选择启用或停用', trigger: 'change'},
          startTime:{ required: true, message: '请选择起始时间', trigger: 'change'},
          endTime:{ required: true, message: '请选择结束时间', trigger: 'change'},
          month:{required: true, message: '请选择月份', trigger: 'blur'},
          days:{ type: 'array', required: true, message: '请至少选择一天', trigger: 'change'},
          // operatData:{required: true, message: '请输入操作数据', trigger: 'blur'},
        },
        yearData:{},
        bigArr: [],
        yearArr: [],
        currentYear: 2018,
        currentUserVlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
      }
    },
    created() {
      this.getscheduleList(); //加载数据
      this.getWholeHoliday(this.$data.currentYear);
      this.initYear();
      
    },
    mounted(){
        var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
    },
    watch:{
        companyId: function (val, oldVal){
            this.getscheduleList(); 
        },
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;
        }
    },
    props: {
        rebackButton: false,
        companyId: '',
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      //获取分页 总条数
      async getscheduleList() {
        try {
          let query = {
            // scheduleName: this.searchInput,
            admincompanyId:this.companyId,
          }
          //分页
          const res = await scheduleList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.scheduleData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              if(item.scheduleStatus == '0'){
                item.scheduleStatus = '启用';
              } else {
                item.scheduleStatus = '停用';
              }
              const tableItem = {
                createAt:item.createAt,
                dataId:item.dataId,
                description:item.description,
                scheduleName:item.scheduleName,
                scheduleStatus:item.scheduleStatus,
                switchContent:item.switchContent,
                switchPhone:item.switchPhone,
                switchStype:item.switchStype,
                timeMessage:item.timeMessage,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vlanId:item.vlanId,
                year:item.year,
              }
              this.scheduleData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      //获取一年的所有年份和对应日期
      async getWholeHoliday(currentYear) {
        try {
          const res = await getWholeHoliday({ year:currentYear});
          if (res.resultCode == '0') {
              this.$data.yearData = res.result;
              this.monthChange(1);
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },
      //一月份样式
      monthChange(month){
          var monthArr = this.$data.yearData[month];
          var week = monthArr[0].weekday;
          this.$data.maxDate = monthArr[0].maxDate;
          var maxDate = monthArr[0].maxDate;
          var firstline = 8 - parseInt(week);
          var bigArr = new Array();
          var firstArr =  new Array();
          var i = 1;
          for(var k=1;k<week;k++){//补位
             firstArr.push({})
          }
          for(i; i <= firstline; i++){
              firstArr.push(monthArr[i]);
          }
          bigArr.push({data:firstArr});
          var j = 0;
          var secondArr =  new Array();
          for(i; i <= maxDate; i++){
              secondArr.push(monthArr[i]);
              j++;
              if(j % 7 == 0){
                  bigArr.push({data:secondArr});
                  j = 0;
                  secondArr = new Array();
              }
          }
          bigArr.push({data:secondArr});
          for(var i=0;i<bigArr.length;i++){
              var arr = bigArr[i].data;
              if(arr != undefined){
                for(var j=0;j<arr.length;j++){
                     var temp = arr[j];
                     bigArr[i].data[j]['checked'] =(temp.holiday!=undefined &&(temp.holiday =='0'|| temp.holiday =='补班')); 
                 }
              }
          }
          this.$data.scheduleForm.days=new Array();
          this.$data.freshkey = month+new Date();
          this.$data.bigArr = bigArr;
          
      },
     
      yearChange(year){
          this.getWholeHoliday(year);
      },
      handSaveParam(args){
        return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getscheduleList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleScheduleSearch() {
        this.getscheduleList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
         }else{
            return '';
         }
      },
      //增加
      handleScheduleAdd() {
        var tempVlanId = this.$data.currentUserVlanId;
        if(this.companyId != undefined && this.companyId != ''){
            tempVlanId = parseInt(this.companyId);
        }
        this.scheduleForm = {
          vlanId: tempVlanId,
          scheduleName: '',
          timeMessage : '',
          scheduleStatus: '0',
          description: '',
          startTime:'08:30',
          endTime:'17:00',
          month:'1',
          days:[],
          operatType:'留言',
          year: this.$data.toyear,
        };
        this.dialogFormVisible = true;
        this.monthChange(1);
      },
      //关闭弹框时重置表单
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deletescheduleByIds(data);
              if (result.resultCode == '0') {
                that.getscheduleList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleScheduleDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deletescheduleByIds(data);
            if (result.resultCode == '0') {
              that.getscheduleList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      async getOperat(args) {
        try {
            const res = await getNotworktime(args);
            if (res.resultCode == '0') {
              //  this.scheduleEditForm.operatData = res.result[0];
               this.scheduleEditForm.operatType = res.result[1];
            }else {
               throw new Error(res.result)
            }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },
      //编辑
      handleScheduleEdit(index, row) {
        this.getOperat({scheduleUuid:row.uuid});
        if(row.timeMessage != ''){
          var timeMessageArray = row.timeMessage.split(" ");
          var month = timeMessageArray[0];
          var days = timeMessageArray[1].split(",");
          var startTime = timeMessageArray[2];
          var endTime = timeMessageArray[3];
        }
        if (typeof row !== 'undefined') {
          this.scheduleEditForm.scheduleName = row.scheduleName;
          this.scheduleEditForm.scheduleStatus = row.scheduleStatus;
          this.scheduleEditForm.description = row.description;
          this.scheduleEditForm.uuid = row.uuid;
          this.scheduleEditForm.month = month;
          this.scheduleEditForm.days = days;
          this.scheduleEditForm.startTime = startTime;
          this.scheduleEditForm.endTime = endTime;
          this.scheduleEditForm.vlanId = row.vlanId;
          this.scheduleEditForm.year = row.year;
        }
        this.dialogFormEdit = true;
      },
      dateFormat(time) {
         if(time != undefined && time != ''){
            return moment().format("YYYY-MM-DD HH:mm:ss")  
         }else{
            return '';
         }
      },
      //查看
      handlescheduleCheck(index, row) {
        this.dialogFormCheck = true;
        row.createAt = this.dateFormat(row.createAt);
        row.updateAt = this.dateFormat(row.updateAt);
        this.$data.showCheck = row;
      },
      //添加 编辑的 保存
      handleScheduleSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //数据拿出来，如果为空则去掉
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              if(formName == 'scheduleEditForm'){
                if(params.scheduleStatus == '启用'){
                  params.scheduleStatus = '0';
                } else {
                  params.scheduleStatus = '1';
                }
              }
              const result = await saveschedule(params);
              if (result.resultCode == '0') {
                  if(formName == 'scheduleForm'){//添加
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getscheduleList();
                  this.$message({
                     type: 'success',
                     message: '工作时间设置保存成功'
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: '保存失败'
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
      rebackEvent(){
          var args =  new Object();
          this.$emit('rebackEvent',args);
      },
      initYear(){
        var today = new Date();
        var year = today.getFullYear();
        var nextYear = year + 1;
        var toyear = {};
        toyear['label'] = year;
        toyear['value'] = year; 
        var next = {};
        next['label'] = nextYear;
        next['value'] = nextYear; 
        this.$data.yearArr.push(toyear);
        this.$data.yearArr.push(next);
        this.$data.toyear = year;
      },
    }
  }

</script>
<style scope lang="less">

