<template>
    <div>
        <el-button class="float-l btn" @click="saveMenu">保存菜单</el-button>
        <el-tree
            :data="menu"
            :props="defaultProps"
            node-key="id"
            :default-expanded-keys="expandArray"
            :expand-on-click-node="false"
            :render-content="renderContent"
            >
        </el-tree>
    </div>
</template>

<script>
 import {
    selectAllMenu,
    savemenuInfo,
  }
  from '@/api/getData';
  export default {
    data() {
      return {
        menu: [],
        uuid:"",
        expandArray:[1],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      }
    },


     created() {
      this.getAllMenu(); //加载菜单数据
    },
    //菜单ID说明：Root节点ID为1不能变，其他二级级别id为101~199.三级节点为（二级ID如11，则为 10101~10199）
    methods: {
        async getAllMenu() {
            try {
                const res = await selectAllMenu();
                if (res.resultCode == '0') {
                    this.uuid = res.result.uuid;
                    this.menu.push(res.result.menu);
                } else {
                    throw new Error(res.message)
                }
            } catch (err) {
                console.log('获取数据失败', err);
            }
        },

        append(data) {
            this.$prompt('请输入菜单名称', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消'
            }).then(({ value }) => {
                let children = data.children;
                let id = 0;
                if(children == undefined || children.length == 0){
                    id = Number.parseInt(data.id + "01");
                }else{
                    let maxId = children[0].id;
                    for(var i = 1;i < children.length;i++){
                        if(children[i].id>maxId){
                            maxId = children[i].id;
                        }
                    }
                    id = maxId+1;
                }
                let newChild = { children: [] };
                newChild['id']=id;
                newChild['label']=value;
                if (!data.children) {
                this.$set(data, 'children', []);
                }
                data.children.push(newChild);
            }).catch(() => {
                 //取消输入
            });
            
        },
        alter(data) {
            this.$prompt('请输入新菜单名称', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消'
            }).then(({ value }) => {
                data['label']=value;
            }).catch(() => {
                 //取消输入
            });
        },
        remove(node, data) {
            const parent = node.parent;
            const children = parent.data.children || parent.data;
            const index = children.findIndex(d => d.id === data.id);
            children.splice(index, 1);
        },
        async saveMenu(){
            //console.log(JSON.stringify(this.$data.menu))
            try {
                var args = new Object();
                if(this.$data.menu.length == 0){
                     this.$message({
                        type: 'info',
                        message: '必须保留根节点'
                    });
                    return;
                }
                args['uuid'] = this.$data.uuid;
                args['menutext'] = JSON.stringify(this.$data.menu[0]);
                const result = await savemenuInfo(args);
                if (result.resultCode == '0') {
                    
                    this.$message({
                        type: 'success',
                        message: '新菜单保存成功'
                    });
                }else{
                    this.$message({
                        type: 'error',
                        message: '保存失败'
                    });
                }
            } catch (err) {
                console.log(err);
                this.$message({
                    type: 'error',
                    message: '保存失败'
                });
            }
        },
        renderContent(h, { node, data, store }) {
            var id = Number.parseInt(data.id); 
            if(id == 1){
                return (
                    <span style="font-size: 14px; padding-right: 8px;">
                    <span>
                    <span>{node.label}</span>
                    </span>
                    <span style="margin-left:100px;">
                    <el-button style="font-size: 12px;" type="text" on-click={ () => this.append(data) }>添加菜单</el-button>
                    </span>
                    </span>
                );
            }else if(id > 999999){
                return (
                    <span style="font-size: 14px; padding-right: 8px;">
                    <span>
                    <span>{node.label}</span>
                    </span>
                    <span style="margin-left:100px;">
                    <el-button style="font-size: 12px; color: #E633FF;" type="text" on-click={ () => this.alter(data) }>修改菜单</el-button>
                    <el-button style="font-size: 12px;margin-left:10px;color: red;" type="text" on-click={ () => this.remove(node, data) }>移除菜单</el-button>
                    </span>
                    </span>
                );
            }else{
                return (
                    <span style="font-size: 14px; padding-right: 8px;">
                    <span>
                    <span>{node.label}</span>
                    </span>
                    <span style="margin-left:100px;">
                    <el-button style="font-size: 12px;" type="text" on-click={ () => this.append(data) }>添加菜单</el-button>
                    <el-button style="font-size: 12px;margin-left:10px;color: #E633FF;" type="text" on-click={ () => this.alter(data) }>修改菜单</el-button>
                    <el-button style="font-size: 12px;margin-left:10px;color: red;" type="text" on-click={ () => this.remove(node, data) }>移除菜单</el-button>
                    </span>
                    </span>
                );
            }  
        }
    }
  };
</script>



