<template>
    <div class="globlgl  globlgl--custom">
        <div class="title">
        <el-form  :label-position="labelPosition" label-width="95px" :model="input" ref="input" :rules="formRules">
            <el-form-item prop="oldPassword" label="当前密码：">
                <el-input type="password" :maxlength="32" v-model="input.oldPassword"  @blur="confirmOldPassword"></el-input>
            </el-form-item>
            <el-form-item prop="newPassword" label="新密码：">
                <el-input type="password" :maxlength="32" v-model="input.newPassword"></el-input>
            </el-form-item>
            <el-form-item prop="newPasswordAgain" label="确认密码：">
                <el-input type="password" :maxlength="32" v-model="input.newPasswordAgain"></el-input>
            </el-form-item>
        </el-form>
        <el-button type="primary" @click="handleExtPasswordSave('input')">确 定</el-button>
        </div>
    </div>
</template>

<script>

  import {
      getExtension,
      setExtPassword,
      confirmCompanyPassword,
      setCompanyPassword,
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
 
  export default {
    data() {

        var checkPassword = (rule, value, callback) => {
            if (value == undefined || "" === value) {
                callback(new Error('请再次输入密码！'));
            }
            if (value != this.input.newPassword) {
                callback(new Error('两次输入密码不一致'));
            } else {
                callback();
            }
        };
        var checkNewPassword = (rule, value, callback) => {
            if (value == undefined || "" === value) {
                callback(new Error('请输入新密码！'));
            }
            if (value == this.input.oldPassword) {
                callback(new Error('新密码不能与当前密码一致！'));
            } else {
                callback();
            }
        };
        return {
            oldPasswordIsRight:false,
            labelPosition: 'right',
            input:{
                oldPassword:'',
                newPassword:'',
                newPasswordAgain:'',
            },
            formRules: {      
                newPasswordAgain: [{required: true,validator:checkPassword, trigger: 'blur'}],
                oldPassword: [{required: true,message: '请输入原密码', trigger: 'blur'}],
                newPassword: [{required: true, validator:checkNewPassword, trigger: 'blur'}],
            },
        }
    },
    created() {
      
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      async confirmOldPassword(){
        try{
          if(this.input.oldPassword != '' && this.input.oldPassword!=undefined){
            //判断分机密码是否正确
            if(this.$store.baseStore.getters.getUserInfo.isextension == true){
              const res = await getExtension({
                extNbr:this.$store.baseStore.getters.getUserInfo.username,
              });
              if(res.resultCode == '0'){
                if(res.result.password != this.input.oldPassword){
                  this.oldPasswordIsRight = false;
                  this.$message({
                    type: 'error',
                    message: '密码错误!'
                  });
                } else {
                  this.oldPasswordIsRight = true;
                }
              } else {
                throw new Error(res.message)
              }
            //判断租户密码是否正确
            } else {
              const res = await confirmCompanyPassword({
                username:this.$store.baseStore.getters.getUserInfo.username,
                password:this.input.oldPassword,
              });
              if(res.resultCode == '0'){
                if(!res.result){
                  this.oldPasswordIsRight = false;
                  this.$message({
                    type: 'error',
                    message: '密码错误!'
                  });
                } else {
                  this.oldPasswordIsRight = true;
                }
              } else {
                throw new Error(res.message)
              }
            }
          }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },
      handleExtPasswordSave(formName) {
        
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            if(!this.oldPasswordIsRight){
              this.$message({
                type: 'error',
                message: '当前密码错误!'
              });
              return ;
            }
            try {
              if(!this.oldPasswordIsRight){
                if(!res.result){
                  this.$message({
                    type: 'error',
                    message: '原密码错误!'
                  });
                }
              }
              if(this.$store.baseStore.getters.getUserInfo.isextension === true){
                const res = await setExtPassword({
                  extNbr:this.$store.baseStore.getters.getUserInfo.username,
                  newPassword:this.input.newPassword,
                });
                if (res.resultCode == '0') {
                    this.$message({
                      type: 'success',
                      message: '修改密码成功！'
                    });
                    this.input.oldPassword = '';
                    this.input.newPassword = '';
                    this.input.newPasswordAgain = '';
                } else {
                    this.$message({
                      type: 'error',
                      message: '保存失败'
                    });
                }
              } else {
                const res = await setCompanyPassword({
                  username:this.$store.baseStore.getters.getUserInfo.username,
                  newPassword:this.input.newPassword,
                });
                if (res.resultCode == '0') {
                    this.$message({
                      type: 'success',
                      message: '修改密码成功！'
                    });
                    this.input.oldPassword = '';
                    this.input.newPassword = '';
                    this.input.newPasswordAgain = '';
                } else {
                    this.$message({
                      type: 'error',
                      message: '保存失败'
                    });
                }
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
    }
  }

</script>
<style scope lang="less">
  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    } 
    .title{
        font-size:100%;
        width: 400px;
    }


  }

 
</style>





