<template>
    <div class="tongji-wrapper">
        <div class="menu tac">
            <el-row>
                <el-col>
                    <el-menu :default-active="defaultMenu" @select="openMenu">
                        <el-submenu index="callControl" v-show="controlmenu">
                            <template slot="title" class="el-menu-title"><i class="tongji-nav-arrow el-icon-caret-right c4c4c4"></i><span>工作时间</span></template>                            
                            <el-menu-item index="notworktime" v-show="notworktimemenu">工作日列表</el-menu-item>
                            <el-menu-item index="schedule" v-show="schedulemenu">工作日设置</el-menu-item>
                        </el-submenu>
                        <el-submenu index="ivr" v-show="ivrmenu">
                            <template slot="title" class="el-menu-title"><i class="tongji-nav-arrow el-icon-caret-right c4c4c4"></i><span>IVR管理</span></template>                            
                            <el-menu-item index="ivrFlowMenu" v-show="ivrFlowMenumenu">IVR菜单管理</el-menu-item>
                            <el-menu-item index="ivrEntry" v-show="ivrEntrymenu">IVR子菜单管理</el-menu-item>
                        </el-submenu>
                        <el-submenu index="blackList" v-show="blackListmenu">
                            <template slot="title" class="el-menu-title"><i class="tongji-nav-arrow el-icon-caret-right c4c4c4"></i><span>黑名单</span></template>                            
                            <el-menu-item index="callerBlackLists" v-show="callerBlackListsmenu">主叫黑名单管理</el-menu-item>
                            <el-menu-item index="calleeBlackLists" v-show="calleeBlackListsmenu">被叫黑名单管理</el-menu-item>
                        </el-submenu>
                        <el-submenu index="company" v-show="companymenu">
                            <template slot="title" class="el-menu-title"><i class="tongji-nav-arrow el-icon-caret-right c4c4c4"></i><span>用户相关管理</span></template>                            
                            <el-menu-item index="user" v-show="usermenu">用户管理</el-menu-item>
                            <el-menu-item index="role" v-show="rolemenu">角色管理</el-menu-item>
                            <el-menu-item index="menuInfo" v-show="menuInfomenu">菜单管理</el-menu-item>
                        </el-submenu>
                        <el-submenu index="config" v-show="configmenu">
                            <template slot="title" class="el-menu-title"><i class="tongji-nav-arrow el-icon-caret-right c4c4c4"></i><span>系统参数管理</span></template>                            
                            <el-menu-item index="systemconfig" v-show="systemconfigmenu">系统参数配置</el-menu-item>
                            <el-menu-item index="uploadfile" v-show="uploadfilemenu">文件上传</el-menu-item>
                            <el-menu-item index="holiday" v-show="holidaymenu">节假日设置</el-menu-item>
                            <!-- <el-menu-item index="callForward" v-show="callForward">呼叫转移</el-menu-item> -->
                        </el-submenu>
                        <el-submenu index="password" v-show="passwordmenu">
                            <template slot="title" class="el-menu-title"><i class="tongji-nav-arrow el-icon-caret-right c4c4c4"></i><span>密码修改</span></template>                            
                            <el-menu-item index="setPassword" v-show="setPasswordmenu">修改密码</el-menu-item>
                            <el-menu-item index="setExtPassword" v-show="setExtPasswordmenu">分机密码修改</el-menu-item>
                        </el-submenu>
                    </el-menu>
                </el-col>
            </el-row>
        </div>
        <div class="main">
            <user v-if="selectMenuName=='user'"></user>
            <role v-if="selectMenuName=='role'"></role>
            <menuInfo v-if="selectMenuName=='menuInfo'"></menuInfo>
            <systemconfig v-if="selectMenuName=='systemconfig'"></systemconfig>
            <uploadfile v-if="selectMenuName=='uploadfile'"></uploadfile>
            <holiday v-if="selectMenuName=='holiday'"></holiday>
            <setExtPassword v-else-if="selectMenuName=='setExtPassword'"></setExtPassword>
            <setExtPassword v-else-if="selectMenuName=='setPassword'"></setExtPassword>
            <notworktime v-else-if="selectMenuName=='notworktime'"></notworktime>
             <schedule v-else-if="selectMenuName=='schedule'"></schedule>
            <!-- <callForward v-if="selectMenuName=='callForward'"></callForward> -->
            <ivrEntry v-if="selectMenuName=='ivrEntry'"></ivrEntry>
            <ivrFlowMenu v-else-if="selectMenuName=='ivrFlowMenu'"></ivrFlowMenu>
            <calleeBlackLists v-else-if="selectMenuName=='calleeBlackLists'"></calleeBlackLists>
            <callerBlackLists v-else-if="selectMenuName=='callerBlackLists'"></callerBlackLists>
        </div>
    </div>
</template>

<script>
import user from "./user.vue"
import role from "./role.vue"
import menuInfo from "./menuInfo.vue"
import systemconfig from "./systemconfig.vue"
import uploadfile from "./uploadfile.vue"
import holiday from "./holiday.vue"
import callForward from "./callForward.vue"
import setExtPassword from './setExtPassword.vue'
import notworktime from './notworktime.vue'
import schedule from './schedule.vue'
import ivrEntry from "./ivrEntry.vue"
import ivrFlowMenu from "./ivrFlowMenu.vue"
import calleeBlackLists from "./calleeBlackLists.vue"
import callerBlackLists from './callerBlackLists.vue'

export default {
    components: {
       user: user,
       role: role,
       menuInfo: menuInfo,
       systemconfig: systemconfig,
       uploadfile: uploadfile,
       holiday: holiday,
       callForward: callForward,
       setExtPassword: setExtPassword,
       schedule:schedule,
       notworktime:notworktime,
       ivrEntry:ivrEntry,
       ivrFlowMenu:ivrFlowMenu,
       calleeBlackLists:calleeBlackLists,
       callerBlackLists:callerBlackLists,
    },
    data() {
        return {
            selectMenuName: '',
            defaultMenu:'',

            configmenu: false,
            companyInfomenu: false,
            companymenu:false,
            usermenu: false,
            rolemenu: false,
            menuInfomenu: false,
            systemconfigmenu: false,
            uploadfilemenu: false,
            holidaymenu:false,
            callForward:false,
	        setExtPasswordmenu:false,
            setPasswordmenu: false,
            controlmenu: false,
            notworktimemenu: false,
            passwordmenu: false,
            schedulemenu: false,
            ivrmenu: false,
            ivrFlowMenumenu: false,
            ivrEntrymenu: false,
            blackListmenu: false,
            callerBlackListsmenu: false,
            calleeBlackListsmenu: false,
        }
    },
    created(){
        let userInfo = this.$store.baseStore.getters.getUserInfo;
        let secondmenu = userInfo.menu.secondmenuset;
        if(secondmenu.indexOf("用户相关管理") != -1){
            this.$data.companymenu = true
        }
        if(secondmenu.indexOf("系统参数管理") != -1){
            this.$data.configmenu = true
        }
        if(secondmenu.indexOf("工作时间") != -1){
             this.$data.controlmenu = true
        }
         if(secondmenu.indexOf("IVR管理") != -1){
            this.$data.ivrmenu = true
        }
        if(secondmenu.indexOf("黑名单") != -1){
             this.$data.blackListmenu = true
        }
        if(secondmenu.indexOf("密码修改") != -1){
             this.$data.passwordmenu = true
        }
        let thirdmenu = userInfo.menu.thirdmenuset;
        let defaultmenu = "";
        
        if(thirdmenu.indexOf("用户管理") != -1){
            this.$data.usermenu = true;
            defaultmenu = "user";
           
        }
        if(thirdmenu.indexOf("角色管理") != -1){
            this.$data.rolemenu = true;
            if(defaultmenu == ""){
                defaultmenu = "role";
            }
        }
        if(thirdmenu.indexOf("菜单管理") != -1){
            this.$data.menuInfomenu = true;
            if(defaultmenu == ""){
                defaultmenu = "menuInfo";
            }
        }
        if(thirdmenu.indexOf("系统参数配置") != -1){
            this.$data.systemconfigmenu = true;
            if(defaultmenu == ""){
                defaultmenu = "systemconfig";
            }
        }
        if(thirdmenu.indexOf("文件上传") != -1){
            this.$data.uploadfilemenu = true;
            if(defaultmenu == ""){
                defaultmenu = "uploadfile";
            }
        }
        if(thirdmenu.indexOf("节假日设置") != -1){
            this.$data.holidaymenu = true;
            if(defaultmenu == ""){
                defaultmenu = "holiday";
            }
        }
        if(thirdmenu.indexOf("呼叫转移") != -1){
            this.$data.callForward = true;
            if(defaultmenu == ""){
                defaultmenu = "callForward";
            }
        }
	
	    if(thirdmenu.indexOf("修改密码") != -1){
            this.$data.setPasswordmenu = true;
            if(defaultmenu == ""){
                defaultmenu = "setExtPassword";
            }
        }
        if(thirdmenu.indexOf("分机密码修改") != -1){
            this.$data.setExtPasswordmenu = true;
            if(defaultmenu == ""){
                defaultmenu = "setExtPassword";
            }
        }
        if(thirdmenu.indexOf("工作日列表") != -1){
             this.$data.notworktimemenu = true
             if(defaultmenu == ""){
                  defaultmenu = "notworktime";
             }
        }
        if(thirdmenu.indexOf("工作日设置") != -1){
            this.$data.schedulemenu = true
            if(defaultmenu == ""){
                  defaultmenu = "schedule";
             }
        }
        if(thirdmenu.indexOf("IVR菜单管理") != -1){
            this.$data.ivrFlowMenumenu = true
            if(defaultmenu == ""){
                defaultmenu = "ivrFlowMenu";
            }
        }
        if(thirdmenu.indexOf("IVR子菜单管理") != -1){
             this.$data.ivrEntrymenu = true
             if(defaultmenu == ""){
                  defaultmenu = "ivrEntry";
             }
        }
	   if(thirdmenu.indexOf("主叫黑名单管理") != -1){
             this.$data.callerBlackListsmenu = true
             if(defaultmenu == ""){
                  defaultmenu = "callerBlackLists";
             }
        }
        if(thirdmenu.indexOf("被叫黑名单管理") != -1){
            this.$data.calleeBlackListsmenu = true
            if(defaultmenu == ""){
                  defaultmenu = "calleeBlackLists";
             }
        }
        this.$data.defaultMenu = defaultmenu;
        this.$data.selectMenuName =  defaultmenu;

        var currentSelectMenu = this.$store.baseStore.getters.getCurrentSelectMenu;
        if(currentSelectMenu != undefined && currentSelectMenu.secondDegreMenu != undefined
            && currentSelectMenu.secondDegreMenu != '' && currentSelectMenu.firstDegreeMenu == 'setting'){
            this.$data.selectMenuName = currentSelectMenu.secondDegreMenu;
            this.$data.defaultMenu = currentSelectMenu.secondDegreMenu;
        }else{
            var menu = new Object();
            menu['secondDegreMenu'] = defaultmenu;
            menu['firstDegreeMenu'] = 'setting';
            this.$store.baseStore.commit('setCurrentSelectMenu',menu);
        }
    },
    methods: {
        /**
         * 打开菜单
         */
        openMenu: function(index, indexPath) {
            this.$data.selectMenuName = index;
            var menu = new Object();
            menu['secondDegreMenu'] = index;
            menu['firstDegreeMenu'] = 'setting';
            this.$store.baseStore.commit('setCurrentSelectMenu',menu);
        },

        // resize 重置菜单和内容高度
        handleWindowResize() {
          const selector = (selectorName) => {
            return document.querySelector(selectorName);
          };
          const height = window.innerHeight - 50;
          
          selector('.main').style.height = height + 'px';
          selector('.menu.tac').style.height = height + 'px';
          selector('.tongji-wrapper').style.height = height + 'px';
        }, 
    },
    mounted() {
      this.handleWindowResize();
      window.addEventListener('resize', this.handleWindowResize);
      var menu = new Object();
      menu['secondDegreMenu'] = 'companyInfo';
      menu['firstDegreeMenu'] = 'setting';
      this.$store.baseStore.commit('setCurrentSelectMenu',menu);
    },
    beforeDestroy () {
      window.removeEventListener('resize', this.handleWindowResize);
    },
}
</script>

<style lang="less">
  @import "../../common/style/imCommon.less";

  .tongji-wrapper {
    #common-wrapper();
    
    position: relative!important;
    & ::-webkit-scrollbar {
      width: 5px;
      background: transparent;
    }
  }

  .tongji-wrapper ::-webkit-scrollbar-track-piece {
    background: rgba(0, 0, 0, 0.2);
  }

  .tongji-wrapper ::-webkit-scrollbar-thumb {
    height: 50px;
    border: none;
    border-radius: 6px;
    background-clip: padding-box;
    background-color: #959595;
    box-shadow: none;
  }

  .tongji-wrapper {
    display:table-cell;
    position:initial;
    width:100%;
    height:calc(~"100% - 48px");
    float:left;
    color:#3e3e3e;
    font-size:14px;
    .tac{
      width:217px;
      border-right:1px solid #e6e6e6;
      background-color:#f4f6f6;
      float:left;
      overflow: hidden;
      .el-menu{
        background:#f4f6f6;
        overflow-y: auto;
      }
      .el-menu-item{
        &:hover{
          background:#ebfbff;
          color:#00a8d7;
          border-right:1px solid #00a8d7;
        }
        &.is-active{
          background:#ebfbff;
          color:#00a8d7;
          border-right:2px solid #00a8d7;
        }
      }
      .el-submenu__icon-arrow {
        &.el-icon-arrow-down{
          &:before{
            display: none;
          }
        }
      }
      .el-submenu {
        .el-submenu__title{
          height:50px;
          line-height:50px;
          margin-left:-6px;
        }
        .el-menu-item-group{
          background:#f4f6f6;
        }
        .el-menu-item{
          height:40px;
          line-height:40px;
        }
      }
      .el-menu-item-group__title{
        margin-top:-15px;
      }
      .el-menu-title{
        &:hover{
          background:#f00;
        }
      }
      //menu
      .el-submenu__title{
        margin-left:-6px;
      }
      .c4c4c4{
        color:#c4c4c4;
        font-size:12px;
      }
    }
    &>.main {
        width: calc(~"100% - 220px");
        height: 100%;
        float: left;
        .component-wrapper {
            position: relative;
            font-size: 14px;
            color: #3e3e3e;
            padding: 15px 30px;
            &>.component-header {
               .title {
                    font-size: 16px;
                    font-weight:bold;
                }
            }
            &>.component-main {
                padding-top: 15px;
                .search-wrapper {
                    &>.search-txt {
                        width: 190px;
                    }
                    &>.el-select {
                        width: 160px;
                    }
                }
                .cardSection-wrapper {
                    .item {
                        width: 100%;
                        border: 1px solid #e6e6e6;
                        border-radius: 3px;
                        .header {
                            height: 36px;
                            font-size: 14px;
                            color: #3e3e3e;
                            font-weight: bold;
                            background-color: #f4f6f6;
                            border-bottom: 1px solid #e6e6e6;
                            text-align: center;
                            line-height: 35px;
                        }
                    }
                }
                .table-wrapper {
                    &>.header {
                        font-size: 14px;
                        color: #3e3e3e;
                    }
                    &>.el-pagination {
                        padding: 0px;
                        padding-top: 25px;
                        text-align: right;
                    }
                }
            }
        }
    }
  }
  .chart-no-data {
    min-height: 150px;
    position: relative;

    .no-data-text {
      position: absolute;
      top: 50%;
      left: 50%;
      color: #6e6e6e;
      transform: translate(-50%, -50%);
    }
  }
  .el-submenu {
    .el-submenu__title {
      span {
        margin-left: 10px;
      }
    }
    .tongji-nav-arrow[class^=el-icon-] {
      margin: 0;
    }
  }
  .el-submenu.is-opened>.el-submenu__title .tongji-nav-arrow.el-icon-caret-right {
    transform: rotate(90deg);
  }
  .tongji-wrapper .search-wrapper > * {
    vertical-align: middle;
    &.search-txt {
      .el-input__icon.el-icon-search {
        left: 0;
      }
      .el-input__inner {
        padding-right: 0;
        padding-left: 35px;
      }
    }
    &.el-button {
      margin-left: 0;
      &.float-right {
        margin-left: 10px;
      }
    }
  }
</style>
<style scoped="scoped" lang="less">
  .main, .menu.tac {
    padding-bottom: 10px;
    overflow: auto;
    box-sizing: border-box;
  }
</style>



